import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs";
import { Category } from "./analytic-category.model";

@Injectable()
export class TreeListMenuActionsService
{
  dataChange = new BehaviorSubject<Category[]>([]);
  private flatNodeMap = new Map<string, Category>();
  get data(): Category[] { return this.dataChange.value; }

  initialize(data: Category[]): void
  {
    this.dataChange.next(data);
  }

  insertItem(parent: Category, name: string, CategoryUID?: string, Children?: Category[]): Category
  {
    if (!parent)
    {
      const newItem = { CategoryUID: CategoryUID, Name: name, SortOrder: 0, Children: [] } as Category;
      this.deleteItem(this.getCategoryByUID(newItem.CategoryUID));
      this.data.push(newItem);
      this.dataChange.next(this.data);
      return newItem;
    }

    const parentNode = this.getParentFromNodes(parent);
    const newItem = { CategoryUID: CategoryUID, Name: name, SortOrder: parent.Children.length + 1, Children: [], } as Category;
    if (!parentNode)
    {
      let parentToModify = this.getCategoryByUID(parent.CategoryUID);
      if (!parentToModify.Children)
      {
        parentToModify.Children = [];
      }

      this.deleteItem(this.getCategoryByUID(newItem.CategoryUID));
      parentToModify.Children.push(newItem);
    } else
    {
      let parentToModify = parentNode.Children.find(item => item.CategoryUID === parent.CategoryUID);
      if (!parentToModify.Children)
      {
        parentToModify.Children = [];
      }
      this.deleteItem(this.getCategoryByUID(newItem.CategoryUID));
      parentToModify.Children.push(newItem);
    }

    this.dataChange.next(this.data);
    return newItem;
  }

  insertItemAbove(node: Category, name: string, CategoryUID?: string, sortOrder?: number): Category
  {
    const parentNode = this.getParentFromNodes(node);
    const newItem = { CategoryUID: CategoryUID, Name: name, SortOrder: sortOrder, Children: [], ParentID: node.ParentID } as Category;
    this.deleteItem(this.getCategoryByUID(newItem.CategoryUID));
    if (parentNode)
    {
      parentNode.Children.splice(parentNode.Children.findIndex(item => item.CategoryUID === node.CategoryUID), 0, newItem);
    } else
    {
      this.data.splice(this.data.findIndex(item => item.CategoryUID === node.CategoryUID), 0, newItem);
    }
    this.dataChange.next(this.data);
    return newItem;
  }

  insertItemBelow(node: Category, name: string, CategoryUID?: string): Category
  {
    const parentNode = this.getParentFromNodes(node);
    const newItem = { CategoryUID: CategoryUID, Name: name, SortOrder: 0, Children: [], ParentID: node.ParentID } as Category;
    this.deleteItem(this.getCategoryByUID(newItem.CategoryUID));
    if (parentNode)
    {
      parentNode.Children.splice(parentNode.Children.findIndex(item => item.CategoryUID === node.CategoryUID) + 1, 0, newItem);
    } else
    {
      this.data.splice(this.data.findIndex(item => item.CategoryUID === node.CategoryUID) + 1, 0, newItem);
    }
    this.dataChange.next(this.data);
    return newItem;
  }

  getParentFromNodes(node: Category): Category
  {
    return this.getCategoryByUID(node.ParentUID);
  }

  updateItem(node: Category, name: string): void
  {
    node.Name = name;
    this.dataChange.next(this.data);
  }

  deleteItem(node: Category): void
  {
    this.deleteNode(this.data, node);
    this.dataChange.next(this.data);
  }

  reviseSortOrderAndGetSortNumber(category: Category): number
  {
    var sortOrder = this.updateSortOrderAndGetSorNumber(this.data, category);
    this.dataChange.next(this.data);
    return sortOrder;
  }

  private updateSortOrderAndGetSorNumber(categoryList: Category[], inputPage: Category): number
  {
    let sortOrder = 0;
    categoryList.forEach((item, index) =>
    {
      item.SortOrder = index + 1;
      if (item.CategoryUID === inputPage.CategoryUID)
        sortOrder = item.SortOrder;
      if (item.Children && item.Children.length > 0)
      {
        let nestSortOrder = this.updateSortOrderAndGetSorNumber(item.Children, inputPage); // Recurse for children
        if (nestSortOrder > 0)
          sortOrder = nestSortOrder;
      }
    });

    return sortOrder;
  }

  copyPasteItem(item: Category, parent: Category): Category
  {
    const newItem = this.insertItem(parent, item.Name, item.CategoryUID);
    if (item.Children)
    {
      item.Children.forEach(child =>
      {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  copyPasteItemAbove(from: Category, to: Category): Category
  {
    const newItem = this.insertItemAbove(to, from.Name, from.CategoryUID, to.SortOrder);
    if (from.Children)
    {
      from.Children.forEach(child =>
      {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  copyPasteItemBelow(from: Category, to: Category): Category
  {
    const newItem = this.insertItemBelow(to, from.Name, from.CategoryUID);
    if (from.Children)
    {
      from.Children.forEach(child =>
      {
        this.copyPasteItem(child, newItem);
      });
    }
    return newItem;
  }

  deleteNode(nodes: Category[], nodeToDelete: Category): void
  {
    const index = nodes.findIndex(item => item.CategoryUID === nodeToDelete.CategoryUID);
    if (index > -1)
    {
      nodes.splice(index, 1);
    } else
    {
      nodes.forEach(node =>
      {
        if (node.Children && node.Children.length > 0)
        {
          this.deleteNode(node.Children, nodeToDelete);
        }
      });
    }
  }

  getCategoryByUID(CategoryUID: string): Category
  {
    return this.flatNodeMap.get(CategoryUID);
  }

  getApplicableCategories(categoryUID: string = ""): Category[]
  {
    const excludeCategory = this.flatNodeMap.get(categoryUID);

    const excludedUIDs = excludeCategory ? this.getAllDescendantCategories(excludeCategory) : new Set();

    const allCategories = Array.from(this.flatNodeMap.values());
    const filteredCategories = allCategories.filter(node => node.Level < 3 && !excludedUIDs.has(node.CategoryUID));

    // Debug: Log the filtering results
    console.log('All categories with levels:', allCategories.map(cat => ({ name: cat.Name, level: cat.Level })));
    console.log('Filtered categories (Level < 3):', filteredCategories.map(cat => ({ name: cat.Name, level: cat.Level })));

    return filteredCategories;
  }

  getAllDescendantCategories(category: Category): Set<string>
  {
    const result = new Set<string>();
    const stack = [category];

    while (stack.length > 0)
    {
      const current = stack.pop()!;
      result.add(current.CategoryUID);
      const children = this.flatNodeMap.get(current.CategoryUID)?.Children || [];
      stack.push(...children);
    }

    return result;
  }

  setFlatMapCategory(categoryUID: string, category: Category): void
  {
    this.flatNodeMap.set(categoryUID, category);
  }

  public buildCategoryMap(categories: Category[]): void
  {
    const recurse = (category: Category) =>
    {
      this.flatNodeMap.set(category.CategoryUID, category);
      for (const child of category.Children)
      {
        recurse(child);
      }
    };
    for (const cat of categories)
    {
      recurse(cat);
    }
  }

  public calculateHierarchy(categories: Category[]): void
  {
    const setLevelAndDepth = (cat: Category, level: number): number =>
    {
      cat.Level = level;
      let maxChildDepth = 0;
      for (const child of cat.Children)
      {
        const childDepth = setLevelAndDepth(child, level + 1);
        const childRelativeDepth = childDepth - level;
        if (childRelativeDepth > maxChildDepth)
        {
          maxChildDepth = childRelativeDepth;
        }
      }
      cat.MaxChildDepth = maxChildDepth;
      return level + maxChildDepth;
    };

    for (const root of categories)
    {
      if (!root.ParentUID)
      {
        setLevelAndDepth(root, 0);
      }
    }
  }

  /**
   * Calculate the maximum depth of a category and its children
   * @param category The category to calculate depth for
   * @returns The maximum depth of the category tree
   */
  calculateMaxDepth(category: Category): number
  {
    if (!category || !category.Children || category.Children.length === 0)
    {
      return 1; // The category itself is level 1
    }

    let maxChildDepth = 0;
    for (const child of category.Children)
    {
      const childDepth = this.calculateMaxDepth(child);
      maxChildDepth = Math.max(maxChildDepth, childDepth);
    }

    return 1 + maxChildDepth; // Current level + max child depth
  }

  /**
   * Check if dropping a category would exceed the maximum nesting levels
   * @param draggedCategory The category being dragged
   * @param targetParentLevel The level of the target parent (0-based)
   * @param maxAllowedLevels The maximum allowed nesting levels (default: 4)
   * @returns true if the drop would exceed the limit, false otherwise
   */
  wouldExceedMaxLevels(draggedCategory: Category, targetParentLevel: number, maxAllowedLevels: number = 4): boolean
  {
    // Calculate the maximum depth of the dragged category and its children
    const maxDepthOfDraggedCategory = this.calculateMaxDepth(draggedCategory);

    // The dragged category will be at: targetParentLevel + 1
    // The maximum level after drop will be: (targetParentLevel + 1) + (maxDepthOfDraggedCategory - 1)
    // Simplified: targetParentLevel + maxDepthOfDraggedCategory
    const resultingMaxLevel = targetParentLevel + maxDepthOfDraggedCategory;

    return resultingMaxLevel > maxAllowedLevels;
  }
}


