import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { firstValueFrom, Subscription } from 'rxjs';
import { IAnalyticsReportAccessService } from '../../../@intermediate/models/analytics-report-access/analytics-report-access.interface';
import { AnalyticsObject, AnalyticsReportAccessMetaData, AnalyticsTypeDictionary, AnalyticsTypeEnum, GetPaginatedFilteredItemsRequest, GetPaginatedFilteredItemsResponse, PostVisibilityRequest, SortColumnTypeEnum, SortDirectionTypeEnum, StatusEnum } from '../../../@intermediate/models/analytics-report-access/analytics-report-access.model';

@Component({
  selector: 'tei-analytics-group-analytics-analytics-report-access',
  templateUrl: './analytics-report-access.component.html',
  styleUrls: ['./analytics-report-access.component.scss'],
})
export class AnalyticsReportAccessComponent implements OnInit, OnDestroy
{
  reportAccesses: AnalyticsObject[] = [];
  dataSource = new Array<AnalyticsObject>();
  metaData = new AnalyticsReportAccessMetaData();
  isSmallScreen = false;
  emptyFilterMessageState = true;
  pageIndex = 0;
  PageSize = 10;
  totalCount = 0;
  AnalyticsTypeEnum = AnalyticsTypeEnum;
  analyticsTypeDictionary = AnalyticsTypeDictionary;
  StatusEnum = StatusEnum;
  Subscription = new Subscription();
  isLoading: boolean;
  @Input() portalId: string;
  request = new GetPaginatedFilteredItemsRequest();
  constructor(
    @Inject('IAnalyticsReportAccessService')
    private reportAccessService: IAnalyticsReportAccessService,
    private responsiveService: ResponsiveService,
    public manageLoadingService: ManageLoadingService,
  ) { }


  async ngOnInit(): Promise<void>
  {
    this.detectScreenSize();
    await this.loadInitialData();
  }

  async loadInitialData(): Promise<void>
  {
    this.request.PortalID = Number(this.portalId);
    this.request.PageSize = this.PageSize;
    this.request.PageNumber = this.pageIndex + 1;
    const response = await this.load();

    if (response && response.ItemsCount > 0)
    {
      this.dataSource = response.AnalyticsObjects;
      this.totalCount = response.ItemsCount;
      this.emptyFilterMessageState = false;
    } else
    {
      this.handleEmptyRequest();
    }
  }
  toggleVisibility(item: AnalyticsObject): void
  {

    const newStatus = item.Status === StatusEnum.Enabled ? StatusEnum.Disabled : StatusEnum.Enabled;
    const request: PostVisibilityRequest = {
      PortalID: Number(this.portalId),
      AnalyticsObjectUID: item.AnalyticsObjectUID,
      Status: newStatus
    };

    this.Subscription.add(this.reportAccessService.postVisibility(request).subscribe({
      next: () =>
      {
        item.Status = newStatus; // Update status locally after API success

      },
      error: (error) => console.error('Error updating status', error)
    }));

  }
  detectScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = ['xs', 'sm', 'md'].includes(screenSize);
  }

  async loadHandler(request: GetPaginatedFilteredItemsRequest): Promise<void>
  {

    this.request = request;
    this.resetPaging();
    const response = await this.load();
    if (response && response.ItemsCount > 0)
    {
      this.dataSource = response.AnalyticsObjects;
      this.totalCount = response.ItemsCount;
      this.emptyFilterMessageState = false;
    }
    else
    {
      this.handleEmptyRequest();
    }
  }

  async load(): Promise<GetPaginatedFilteredItemsResponse | void>
  {
    try
    {
      this.isLoading = true;
      const cleanedRequest = this.removeNullProperties(this.request) as GetPaginatedFilteredItemsRequest;
      
      const response = await firstValueFrom(
        this.reportAccessService.getReportAccessData(cleanedRequest)
      );
      return response;
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.isLoading = false;
    }
  }

  private removeNullProperties(obj: GetPaginatedFilteredItemsRequest): GetPaginatedFilteredItemsRequest {
    const result: any = {};
    for (const [key, value] of Object.entries(obj)) {
      if (value !== null) {
        result[key] = value;
      }
    }
    return result as GetPaginatedFilteredItemsRequest;
  }

 

  private resetPaging(): void
  {
    this.pageIndex = 0;
    this.request.PortalID = Number(this.portalId);
    this.request.PageSize = this.PageSize;
    this.request.PageNumber = this.pageIndex + 1;
  };

  private handleEmptyRequest(): void
  {
    this.dataSource = [];
    this.totalCount = 0;
    this.emptyFilterMessageState = true;

  }

  async handlePaginationEvent(event: { pageIndex: number; pageSize: number; }): Promise<void>
  {
    this.pageIndex = event.pageIndex;
    this.PageSize = event.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
    this.request.PageSize = this.PageSize;
    const response = await this.load();
    if (response && response.ItemsCount > 0) {
      this.dataSource = response.AnalyticsObjects;
      this.totalCount = response.ItemsCount;
      this.emptyFilterMessageState = false;
    } else {
      this.handleEmptyRequest();
    }
  }
  ngOnDestroy(): void
  {
    this.Subscription?.unsubscribe();
  }

  sortData(sort: Sort): void
  {
    if (!sort.active || sort.direction === '')
    {
      this.request.SortColumn = null;
      this.request.SortDirection = null;
    } else
    {
      let columnName = sort.active;
      this.request.SortColumn = SortColumnTypeEnum[columnName];
      this.request.SortDirection = sort.direction === 'asc' ? SortDirectionTypeEnum.ASC : SortDirectionTypeEnum.DESC;
    }
    this.loadHandler(this.request);
  }

}
