import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { ResponsiveService } from '@tei/core';
import { firstValueFrom, Subscription } from 'rxjs';

import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { DynamicTableInputConfig } from '@tei/material';
import { IAnalyticsLookupService } from '../../../@intermediate/models/reports/lookup.interface';

@Component({
  selector: 'tei-analytics-group-analytics-advanced-lookup-search',
  templateUrl: './advanced-lookup-search.component.html',
  styleUrls: ['./advanced-lookup-search.component.scss']
})
export class AdvancedLookupSearchComponent implements OnInit, OnDestroy
{
  isMobile = false;
  subscription = new Subscription();
  LookupDescription = 'ANALYTICS.REPORT_GENERATOR.ADVANCED_LOOKUP_SEARCH_TITLE';
  tableConfig: DynamicTableInputConfig;
  isLoading = true;
  reportData: any;
  selectedItems: any;
  AllowMultiSelect: boolean;
  constructor(
    private route: ActivatedRoute,
    @Inject('IAnalyticsLookupService') private service: IAnalyticsLookupService,
    public dialog: MatDialogRef<AdvancedLookupSearchComponent>,
    private responsiveService: ResponsiveService,
    @Inject(MAT_DIALOG_DATA) public data
  ) { }

  ngOnInit()
  {

    this.getScreenSize();
    this.loadReportData().then();
  }

  getScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize == 'xs' || screenSize == 'sm' || screenSize == 'md';

  }

  private async loadReportData(): Promise<void>
  {
    this.isLoading = true;

    try
    {
      const response = await firstValueFrom(this.service.Get({ LookupName: this.data.field.LookupSource }));

      this.LookupDescription = response.LookupDescription;
      this.selectedItems = this.data.selectedValues;
      this.AllowMultiSelect = response.AllowMultiSelect;
      this.tableConfig = {
        Data: response.Data[this.data.field.LookupSource],
        IsMultiSelect: response.AllowMultiSelect,
        ShowSelectionColumn: response.AllowMultiSelect,
        SelectedValueField: response.LookupField,
        DefaultSelectedValues: this.data.selectedValues,
        IsInDialog: true,
        ShowMobileView: true
      };

    } catch (err)
    {
      console.error(err);
    } finally
    {
      this.isLoading = false;
    }

  }

  selectionChangeEvent($event: any): void
  {
    if (this.AllowMultiSelect)
    {
      this.selectedItems = $event;
    } else
    {
      if ($event)
      {
        this.selectedItems = [$event];
      } else
      {
        this.selectedItems = [];
      }
    }
  }

  apply(): void
  {
    this.dialog.close(this.selectedItems);
  }

  close(): void
  {
    this.dialog.close(this.data.selectedValues);
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }
}
