import { Component, Inject, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService, ResponsiveService } from '@tei/core';
import { BreadCrumb } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { GetDashboardOperationDetailRequest, GetDashboardOperationDetailResponse, GolfScorecardDashboardModel, OperationDetailFormatType } from '../../../../@intermediate/models/dashboard/dashboard-detail/dashboard-detail.model';
import { DashboardDetailService } from '../../../../@services/reports/dashboard/dashboard-detail.service';

@Component({
  selector: 'tei-analytics-group-analytics-dashboard-details',
  templateUrl: './dashboard-details.component.html',
  styleUrls: ['./dashboard-details.component.scss']
})
export class DashboardDetailsComponent implements OnInit
{
  isLoading: boolean;
  request = new GetDashboardOperationDetailRequest();
  golfScorecardDashboardDetail: GolfScorecardDashboardModel;
  isSmallScreen = false;
  selectedFormat: string = this.translate.instant('ANALYTICS.DASHBOARDS.DASHBOARD_DETAIL.DETAILS');

  constructor(
    private translate: TranslateService,
    @Inject('IAnalyticsDashboardDetailService') private dashboardDetailService: DashboardDetailService,
    public manageLoadingService: ManageLoadingService,
    private responsiveService: ResponsiveService,
  )
  {
    this.request.date = new Date().toISOString().split('T')[0];
    this.request.formatType = OperationDetailFormatType.Daily;
  }

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.DASHBOARDS'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.DASHBOARDS_TITLE'),
      relativeUrl: ''
    }
  ];

  async ngOnInit(): Promise<void>
  {
    this.detectScreenSize();
    await this.loadData();
    // this.route.queryParams.subscribe(params => {
    //   if (params['formatType']) {
    //     this.request.formatType = parseInt(params['formatType']);
    //     this.loadData();
    //   }
    // });

  }

  detectScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = ['xs', 'sm', 'md'].includes(screenSize);
  }

  async loadData(): Promise<GetDashboardOperationDetailResponse | void>
  {
    try
    {
      this.isLoading = true;
      const response = await firstValueFrom(
        this.dashboardDetailService.getDashboardDetail(this.request)
      );

      if (response)
      {
        this.golfScorecardDashboardDetail = response.GolfScorecardDashboardDetail;
      }
      return response;
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.isLoading = false;
    }
  }

  async onFilterChange(filter: GetDashboardOperationDetailRequest)
  {
    this.request.formatType = filter.formatType;
    this.request.date = filter.date;
    await this.loadData();
  }
}
