import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IAnalyticsMyReportsService } from '../../../../../@intermediate/models/reports/my-reports.interface';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { TranslateService } from '@ngx-translate/core';
import { DomSanitizer } from '@angular/platform-browser';
import { HttpClient } from '@angular/common/http';
import { PDFDocumentProxy } from 'ng2-pdf-viewer';

@Component({
  selector: 'tei-analytics-group-analytics-pdf-view',
  templateUrl: './pdf-view.component.html',
  styleUrls: ['./pdf-view.component.scss']
})
export class PdfViewComponent implements OnInit
{
  reportId: string;
  reportUID: string;
  reportLink: string;
  pdfSrc: any;
  reportName: string;
  isLoading = true;
  page = 1;
  totalPages = 0;
  zoom = 1;
  breadCrumbData: BreadCrumb[] = [];

  constructor(
    private snackBarService: SnackBarService,
    private route: ActivatedRoute,
    private router: Router,
    private translateService: TranslateService,
    private http: HttpClient,
    @Inject('IAnalyticsMyReportsService') private myReportService: IAnalyticsMyReportsService,
  ) { }

  ngOnInit(): void
  {
    this.reportUID = this.route.snapshot.paramMap.get('id');
    this.initializeBreadcrumbs();
    this.loadReportData();
  }

  private initializeBreadcrumbs(): void
  {
    const currentUrl = this.router.url;
    const isGenerationLog = currentUrl.includes('/settings/');

    if (isGenerationLog)
    {
      this.breadCrumbData = [
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
          relativeUrl: '',
        },
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.SETTINGS'),
          relativeUrl: '../../',
        },
        {
          title: this.translateService.instant('ANALYTICS.SETTINGS.GENERATION_LOG'),
          relativeUrl: '../../../generation-log',

        },
        {
          title: this.translateService.instant('ANALYTICS.REPORTS.REPORTS_NAV.PDF_REPORT_VIEWER'),
          relativeUrl: '',

        }
      ];
    } else
    {
      this.breadCrumbData = [
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
          relativeUrl: '',

        },
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.REPORTS'),
          relativeUrl: '../../',

        },
        {
          title: this.translateService.instant('ANALYTICS.REPORTS.REPORTS_NAV.MY_REPORTS'),
          relativeUrl: '../../../my-reports',

        },
        {
          title: this.translateService.instant('ANALYTICS.REPORTS.REPORTS_NAV.PDF_REPORT_VIEWER'),
          relativeUrl: '',

        }
      ];
    }
  }

  private loadReportData(): void
  {
    this.isLoading = true;
    this.myReportService.getGeneratedReportLog({ AnalyticsQueueUID: this.reportUID })
      .subscribe({
        next: (response) =>
        {
          if (response.ReportLink)
          {
            this.reportLink = response.ReportLink;
            this.reportName = response.ReportName;
            this.reportId = response.ReportID;

            if (!response.ReportLink)
            {
              this.snackBarService.showError(this.translateService.instant('ANALYTICS.REPORTS.DATA_VIEW.NO_DATA_FOUND'), '', null, 'left', 'bottom', 5);
              return;
            }
            this.loadPdf();
          }
        },
        error: (error) =>
        {
          console.error('Error loading report:', error);
          this.isLoading = false;
        }
      });
  }

  private loadPdf(): void
  {
    this.http.get(this.reportLink, { responseType: 'arraybuffer' })
      .subscribe({
        next: (data: ArrayBuffer) =>
        {
          if (!data?.byteLength)
          {
            this.snackBarService.showError(this.translateService.instant('ANALYTICS.REPORTS.DATA_VIEW.NO_DATA_FOUND'), '', null, 'left', 'bottom', 5);
            return;
          }
          this.pdfSrc = { data };
          this.isLoading = false;
        },
        error: (error) =>
        {
          console.error('Error loading PDF:', error);
          this.isLoading = false;
        }
      });
  }

  onPdfLoadComplete(pdf: PDFDocumentProxy): void
  {
    this.totalPages = pdf.numPages;
  }

  zoomIn(): void
  {
    this.zoom = Math.min(this.zoom + 0.25, 3);
  }

  zoomOut(): void
  {
    this.zoom = Math.max(this.zoom - 0.25, 0.5);
  }

  downloadPDF(): void
  {
    if (this.reportLink)
    {
      this.http.get(this.reportLink, { responseType: 'blob' }).subscribe(blob =>
      {
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `report-${ this.reportName }.pdf`;
        link.click();
        window.URL.revokeObjectURL(url);
      });
    }
  }

  onError(error: any)
  {
    console.error('PDF Viewer Error:', error);
  }
}
