import { Inject, Injectable } from "@angular/core";
import { IAnalyticsDashboardDetailService } from "../../../@intermediate/models/dashboard/dashboard-detail/dashboard-detail.interface";
import { Observable } from "rxjs";
import { GetDashboardOperationDetailRequest, GetDashboardOperationDetailResponse } from "../../../@intermediate/models/dashboard/dashboard-detail/dashboard-detail.model";
import { HttpClient } from "@angular/common/http";
import { IAPIBaseUrlProvider } from "@tei/core";

@Injectable()
export class DashboardDetailService implements IAnalyticsDashboardDetailService {
  private readonly serviceUrl: string;
  constructor( @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
  protected http: HttpClient) {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Dashboard';
  }

  getDashboardDetail(request: GetDashboardOperationDetailRequest): Observable<GetDashboardOperationDetailResponse> {
    const apiUrl = this.serviceUrl + '/Detail';
    return this.http.get<GetDashboardOperationDetailResponse>(apiUrl, {params: request as any});
  }

}
