import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { AnalyticsComponent } from './analytics/analytics.component';
import { PagesComponent } from './pages.component';

const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {
        path: '',
        component: AnalyticsComponent,
      },
      {
        path: 'dashboards',
        loadChildren: () => import('./analytics/dashboards/dashboards.module').then(m => m.DashboardsModule),
        data: { RouteData: new RouteData({ Title: 'ANALYTICS_DASHBOARD' }) },
      },
      {
        path: 'reports',
        loadChildren: () => import('./analytics/reports/reports.module').then(m => m.ReportsModule),
        data: { RouteData: new RouteData({ Title: 'ANALYTICS_REPORTS' }) },
      },
      {
        path: 'settings',
        loadChildren: () => import('./analytics/settings/settings.module').then(m => m.SettingsModule),
        data: { RouteData: new RouteData({ Title: 'ANALYTICS_SETTINGS' }) },
      },
      { path: '**', redirectTo: '' }
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule
{
}
