import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { Sort } from '@angular/material/sort';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { CardDialogComponent, ConfirmationDialogComponent, SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { AnalyticsTypeDictionary, AnalyticsTypeEnum } from '../../../@intermediate/models/analytics-report-access/analytics-report-access.model';
import { IAnalyticsMyReportsService } from '../../../@intermediate/models/reports/my-reports.interface';
import
{
  AnalyticsQueueSortColumnType,
  BaseReportRequest,
  BaseReportResponse,
  GetGeneratedReportListLogRequest,
  GetMyReportsRequest,
  IAnalyticsQueue,
  MyReportMetadata,
  SortDirectionTypeEnum,
  StatusTypeDictionary,
  StatusTypeEnum
} from '../../../@intermediate/models/reports/new-reports.model';

@Component({
  selector: 'tei-analytics-group-analytics-my-report-list',
  templateUrl: './my-report-list.component.html',
  styleUrls: ['./my-report-list.component.scss']
})
export class MyReportListComponent implements OnInit, OnDestroy
{
  isSmallScreen = false;
  emptyFilterMessageState = true;
  dataSource = new Array<IAnalyticsQueue>();
  metadata = new MyReportMetadata();
  totalCount = 0;
  pageIndex = 0;
  pageSize = 10;
  isLoading: boolean;
  AnalyticsTypeEnum = AnalyticsTypeEnum;
  StatusTypeEnum = StatusTypeEnum;
  statusTypeDictionary = StatusTypeDictionary;
  @Input() isGeneratedLog = false;

  analyticsTypeDictionary = AnalyticsTypeDictionary;
  request: BaseReportRequest = new BaseReportRequest();
  subscription = new Subscription();

  constructor(
    @Inject('IAnalyticsMyReportsService') private myReportsService: IAnalyticsMyReportsService,
    private responsiveService: ResponsiveService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private translateService: TranslateService,
    private snackbarService: SnackBarService
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.detectScreenSize();
    await this.loadInitialData();
  }

  detectScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = ['xs', 'sm', 'md'].includes(screenSize);
  }

  async loadHandler(request: GetMyReportsRequest): Promise<void>
  {
    this.request = request;
    this.resetPaging();

    const response = await this.load();
    if (response && response.ItemsCount > 0)
    {
      this.updateDataFromResponse(response);
    } else
    {
      this.handleEmptyRequest();
    }
  }

  async load(): Promise<BaseReportResponse | void>
  {
    try
    {
      this.isLoading = true;
      if (this.isGeneratedLog)
      {
        return await firstValueFrom(
          this.myReportsService.getGetGeneratedReportList(this.request as GetGeneratedReportListLogRequest)
        );
      }
      return await firstValueFrom(
        this.myReportsService.getMyReportListData(this.request)
      );
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.isLoading = false;
    }
  }

  private updateDataFromResponse(response: BaseReportResponse): void
  {
    this.dataSource = response.AnalyticsQueues;
    this.totalCount = response.ItemsCount;
    this.emptyFilterMessageState = false;
  }

  private resetPaging(): void
  {
    this.pageIndex = 0;
    this.request.PageSize = this.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
  }

  async loadInitialData(): Promise<void>
  {
    this.request = new (this.isGeneratedLog ? GetGeneratedReportListLogRequest : GetMyReportsRequest)();
    this.request.PageSize = this.pageSize;
    this.request.PageNumber = this.pageIndex + 1;

    const response = await this.load();
    if (response && response.ItemsCount > 0)
    {
      this.updateDataFromResponse(response);
    } else
    {
      this.handleEmptyRequest();
    }
  }

  private handleEmptyRequest(): void
  {
    this.dataSource = [];
    this.totalCount = 0;
    this.emptyFilterMessageState = true;
  }

  async handlePaginationEvent(event: { pageIndex: number; pageSize: number; }): Promise<void>
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
    this.request.PageSize = this.pageSize;
    const response = await this.load();
    if (response && response.ItemsCount > 0)
    {
      this.updateDataFromResponse(response);
    } else
    {
      this.handleEmptyRequest();
    }
  }

  onViewReport(report: IAnalyticsQueue): void
  {
    const viewType = report.AnalyticsType === AnalyticsTypeEnum.PDF ? 'pdf' : 'data-view';
    const basePath = this.isGeneratedLog ? 'view' : 'view';
    this.router.navigate([basePath, viewType, report.AnalyticsQueueUID], { relativeTo: this.activatedRoute.parent });
  }

  onManageReport(report: IAnalyticsQueue): void
  {
    this.router.navigate(['report-generator', report.AnalyticsObjectUID], { relativeTo: this.activatedRoute.parent });
  }

  onCancelReport(AnalyticsQueueUID: String): void
  {
    const dialogContent = {
      title: this.translateService.instant('ANALYTICS.REPORTS.MY_REPORTS.CANCEL'),
      content: this.translateService.instant('ANALYTICS.REPORTS.MY_REPORTS.CANCEL_REPORT_MESSAGE'),
      submitButtonLabel: this.translateService.instant('ANALYTICS.REPORTS.MY_REPORTS.CANCEL'),
      cancelButtonLabel: this.translateService.instant('ANALYTICS.REPORTS.MY_REPORTS.NO'),
      submitButtonColor: 'warn'
    };

    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: 'o-confirm-dialog',
      data: {
        component: ConfirmationDialogComponent,
        dialogData: dialogContent
      },
      width: '350px',
    });

    this.subscription.add(dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.cancelReport(AnalyticsQueueUID);
      }
    }));
  }

  async cancelReport(AnalyticsQueueUID: String): Promise<void>
  {
    try
    {
      await firstValueFrom(this.myReportsService.CancelReport(AnalyticsQueueUID));
      this.snackbarService.showSuccess(this.translateService.instant('ANALYTICS.REPORTS.MY_REPORTS.CANCEL_REPORT_SUCCESSFULLY'), "", null, "left", "bottom");
      await this.loadInitialData();
    } catch (error)
    {
      console.error('Error cancelling report:', error);
    }
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

  async sortData(sort: Sort): Promise<void>
  {
    if (!sort.active)
    {
      this.request.SortColumn = null;
      this.request.SortDirection = null;
    } else
    {
      // Map the mat-sort-header value to the enum value
      let columnName = sort.active;
      if (columnName === 'ID')
      {
        columnName = 'AnalyticsQueueID';
      }

      this.request.SortColumn = AnalyticsQueueSortColumnType[columnName];
      this.request.SortDirection = sort.direction === 'asc' || sort.direction === '' ? SortDirectionTypeEnum.DESC : SortDirectionTypeEnum.ASC;
    }
    await this.loadHandler(this.request);
  }
}
