import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';

@Component({
  selector: 'tei-analytics-group-analytics-new-reports',
  templateUrl: './new-reports.component.html',
  styleUrls: ['./new-reports.component.scss']
})
export class NewReportsComponent implements OnInit {
  breadCrumbData: BreadCrumb[] = [];

  constructor(private translate: TranslateService) { }

  ngOnInit(): void {
    this.initializeBreadcrumbs();
  }

  private initializeBreadcrumbs(): void {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
        relativeUrl: ''
      },
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.REPORTS'),
        relativeUrl: '../'
      },
      {
        title: this.translate.instant('ANALYTICS.REPORTS.REPORTS_NAV.NEW_REPORTS'),
        relativeUrl: ''
      }
    ];
  }
}
