import { Inject, Injectable } from "@angular/core";
import { Observable, of } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { IAPIBaseUrlProvider } from "@tei/core";
import { IAnalyticsReportAccessService } from "../@intermediate/models/analytics-report-access/analytics-report-access.interface";
import { GetPaginatedFilteredItemsResponse, GetPaginatedFilteredItemsRequest, AnalyticsObject, DepartmentTypesResponse, GetDepartmentTypesRequest, PostVisibilityRequest } from "../@intermediate/models/analytics-report-access/analytics-report-access.model";

@Injectable()
export class AnalyticsReportAccessService implements IAnalyticsReportAccessService {
  private readonly serviceUrl: string;
  constructor( @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
  protected http: HttpClient) {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Object';
  }

  getConfigDepartmentTypes(request: GetDepartmentTypesRequest): Observable<DepartmentTypesResponse> {
    const apiUrl = this.serviceUrl + '/config';
    return this.http.get<DepartmentTypesResponse>(apiUrl, { params: request as any });
  }

  postVisibility(request: PostVisibilityRequest) {
    const apiUrl = this.serviceUrl;
    return this.http.post<GetPaginatedFilteredItemsResponse>(apiUrl,request);
  }

  getReportAccessData(request: GetPaginatedFilteredItemsRequest): Observable<GetPaginatedFilteredItemsResponse> {
    const apiUrl = this.serviceUrl + '/GetPaginatedFilteredItems';
    return this.http.post<GetPaginatedFilteredItemsResponse>(apiUrl,request);
  }
}
