import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AnalyticsCategoryComponent } from '../category/analytics-category.component';
import { DataViewComponent } from '../reports/view/data-view/data-view.component';
import { PdfViewComponent } from '../reports/view/pdf-view/pdf-view.component';
import { GenerationLogComponent } from './generation-log/generation-log.component';
import { SecurityComponent } from './security/security.component';
import { SettingsNavigationComponent } from './settings-navigation/settings-navigation.component';
import { SettingsComponent } from './settings.component';

const routes: Routes = [
  {
    path: '', component: SettingsComponent,
    children: [
      { path: '', component: SettingsNavigationComponent },
      { path: 'security', component: SecurityComponent },
      { path: 'security/manage-category', component: AnalyticsCategoryComponent },
      { path: 'generation-log', component: GenerationLogComponent },
      { path: 'view/pdf/:id', component: PdfViewComponent },
      { path: 'view/data-view/:id', component: DataViewComponent },
      { path: '**', redirectTo: '' }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SettingsRoutingModule
{
}
