import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardSummaryComponent } from './dashboard-summary.component';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { ResponsiveTableHeightModule } from '@tei/core';
import { MatIconModule } from '@angular/material/icon';

const COMPONENTS = [DashboardSummaryComponent];
const TEI_MATERIAL_MODULES = [MatCardModule, MatTableModule, MatIconModule];
const BASE_MODULES = [CommonModule];
const TEI_CORE_MODULES = [TeiDateTranslateModule, ResponsiveTableHeightModule, TeiTextTranslatePipeModule,];
@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...TEI_MATERIAL_MODULES,
    ...BASE_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...COMPONENTS
  ]
})
export class DashboardSummaryModule { }
