import { Component, OnInit } from '@angular/core';

import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';

@Component({
  selector: 'tei-analytics-group-analytics-my-report',
  templateUrl: 'my-report.component.html',
  styleUrls: ['my-report.component.scss']
})
export class MyReportComponent implements OnInit {
  breadCrumbData: BreadCrumb[] = [];
  isGeneratedLog: boolean = false;
  constructor(private translate: TranslateService) { }

  ngOnInit(): void {
    this.initializeBreadcrumbs();
    this.isGeneratedLog = false;
  }

  private initializeBreadcrumbs(): void {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
        relativeUrl: ''
      },
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.REPORTS'),
        relativeUrl: '../'
      },
      {
        title: this.translate.instant('ANALYTICS.REPORTS.REPORTS_NAV.MY_REPORTS'),
        relativeUrl: ''
      }
    ];
  }
}
