import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MyReportComponent } from './my-report.component';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, CardDialogModule, FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { MyReportsDetailsModule } from '../../../../components/my-reports-details/my-reports-details.module';
import { PdfViewModule } from '../view/pdf-view/pdf-view.module';
import { DataViewModule } from '../view/data-view/data-view.module';

const COMPONENTS = [MyReportComponent];
const BASE_MODULES = [CommonModule];
const TEI_MATERIAL_MODULES = [QuickSearchFilterChipModule, FilterChipsModule, CardDialogModule, LoadingModule, BreadCrumbModule];
const MATERIAL_MODULES = [MatCheckboxModule, MatCardModule, MatTableModule, MatInputModule,
  MatPaginatorModule, MatMenuModule, MatTooltipModule, MatTabsModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const REPORTS_MODULE = [MyReportsDetailsModule, PdfViewModule, DataViewModule];

@NgModule({
  declarations: [
    ...COMPONENTS,
  ],
  imports: [
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...REPORTS_MODULE
  ],
  exports: [
    ...COMPONENTS,
  ]
})
export class MyReportModule { }
