import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { DataViewComponent } from './data-view.component';
import { MatCardModule } from '@angular/material/card';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatIconModule } from '@angular/material/icon';
import { MatTooltipModule } from '@angular/material/tooltip';
import { BreadCrumbModule, DynamicTableModule } from '@tei/material';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';
import { HttpClientModule } from '@angular/common/http';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';

const TEI_MATERIAL_MODULES = [DynamicTableModule, BreadCrumbModule];
const MATERIAL_MODULES = [
  MatCardModule,
  MatButtonModule,
  MatIconModule,
  MatProgressSpinnerModule,
  MatTooltipModule,
  MatCheckboxModule
];

const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const BASE_MODULES = [CommonModule, RouterModule, HttpClientModule,];

@NgModule({
  declarations: [DataViewComponent],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES

  ],
  exports: [DataViewComponent]
})
export class DataViewModule { }
