import { Component, Inject, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { IAnalyticsDashboardService } from '../../../@intermediate/models/dashboard/dashboard.interface';
import { ManageLoadingService } from '@tei/core';
import { firstValueFrom } from 'rxjs';
import { GetDashboardOperationsRequest, GetDashboardOperationsResponse, Operation } from '../../../@intermediate/models/dashboard/dashboard.model';


@Component({
  selector: 'tei-dashboards',
  templateUrl: './dashboards.component.html',
  styleUrls: ['./dashboards.component.scss'],
})
export class DashboardsComponent implements OnInit
{
  isLoading: boolean;
  request = new GetDashboardOperationsRequest();
  operations: Operation[] = [];
  constructor(private translate: TranslateService,
    @Inject('IAnalyticsDashboardService') private dashboardService: IAnalyticsDashboardService,
    public manageLoadingService: ManageLoadingService,
  )
  {
  }


  breadCrumbData: BreadCrumb[] = [

    {
      title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.DASHBOARDS'),
      relativeUrl: ''
    }
  ];

  async ngOnInit(): Promise<void> {
    await this.loadData();
  }

  async loadData(): Promise<GetDashboardOperationsResponse | void> {
    try {
      this.isLoading = true;
      const response = await firstValueFrom(
        this.dashboardService.getDashboardOperations(this.request)
      );
      if(response && response.Operations.length > 0)
      {
        this.operations = response.Operations;
      }
      return response;
    } catch (error) {
      console.error(error);
    } finally
    {
      this.isLoading = false;
    }
  }
}
