import { Observable } from "rxjs";
import { IAnalyticsDashboardService } from "../../../@intermediate/models/dashboard/dashboard.interface";
import { GetDashboardOperationsRequest, GetDashboardOperationsResponse } from "../../../@intermediate/models/dashboard/dashboard.model";
import { Inject, Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { IAPIBaseUrlProvider } from "@tei/core";

@Injectable()
export class AnalyticsDashboardService implements IAnalyticsDashboardService {
  private readonly serviceUrl: string;
  constructor( @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
  protected http: HttpClient) {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics';
  }
  getDashboardOperations(request: GetDashboardOperationsRequest): Observable<GetDashboardOperationsResponse> {
    const apiUrl = this.serviceUrl + '/Dashboard';
    return this.http.get<GetDashboardOperationsResponse>(apiUrl);
  }

}