import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DetailsFilterHeaderComponent } from './details-filter-header.component';
import { MatNativeDateModule, MAT_DATE_FORMATS, MAT_DATE_LOCALE, DateAdapter } from '@angular/material/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatIconModule } from '@angular/material/icon';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { MomentDateAdapter, MAT_MOMENT_DATE_ADAPTER_OPTIONS } from '@angular/material-moment-adapter';

export const MY_FORMATS = {
  parse: {
    dateInput: 'LL',
  },
  display: {
    dateInput: 'LL',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY',
  },
};

const COMPONENTS = [DetailsFilterHeaderComponent];
const BASE_MODULES = [
  CommonModule,
  ReactiveFormsModule,
  MatNativeDateModule,
  FormsModule
];
const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const MATERIAL_MODULES = [MatIconModule, MatDatepickerModule, MatDatepickerModule]
const TEI_MATERIAL_MODULES = [MatCardModule,
  MatFormFieldModule,
  MatSelectModule,
  MatInputModule,]

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  providers: [
    {
      provide: DateAdapter,
      useClass: MomentDateAdapter,
      deps: [MAT_DATE_LOCALE, MAT_MOMENT_DATE_ADAPTER_OPTIONS]
    },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
    { provide: MAT_DATE_LOCALE, useValue: 'en-US' }
  ],
  exports: [...COMPONENTS]
})
export class DetailsFilterHeaderModule { }
