import { FlatTreeControl } from '@angular/cdk/tree';
import { Component, ElementRef, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatMenuTrigger } from '@angular/material/menu';
import { MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { IAnalyticCategoryService } from '../../../@intermediate/models/category/analytic-category.interface';
import { ApplicableRole, Category, CategoryDialogData, DeleteCategoryRequest, DropOverAreaType, FlatNode, PostDetailedCategoryRequest, TodoItemFlatNode, UpdateCategorySortOrderRequest } from '../../../@intermediate/models/category/analytic-category.model';
import { TreeListMenuActionsService } from '../../../@intermediate/models/category/tree-list-menu-actions.service';
import { DeleteCategoryDialogComponent } from '../../../components/manage-category-dialog/delete-category-dialog/delete-category-dialog.component';
import { ManageCategoryDialogComponent } from '../../../components/manage-category-dialog/manage-category-dialog.component';


@Component({
  selector: 'tei-analytics-group-analytics-category',
  templateUrl: './analytics-category.component.html',
  styleUrls: ['./analytics-category.component.scss']
})
export class AnalyticsCategoryComponent implements OnInit, OnDestroy
{
  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
      relativeUrl: ''
    },
    {
      title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.SETTINGS'),
      relativeUrl: '../../'
    },
    {
      title: this.translateService.instant('ANALYTICS.SETTINGS.SECURITY'),
      relativeUrl: '../'
    },
    {
      title: this.translateService.instant('ANALYTICS.SECURITY.CATEGORIES'),
      relativeUrl: './'
    }
  ];
  @ViewChild(MatMenuTrigger) contextMenuTrigger!: MatMenuTrigger;
  staffRoles: ApplicableRole[] = [];
  treeControl: FlatTreeControl<TodoItemFlatNode>;
  treeFlattener: MatTreeFlattener<Category, TodoItemFlatNode>;
  dataSource: MatTreeFlatDataSource<Category, TodoItemFlatNode>;
  selectedCategory: Category | null = null;
  isLoading = false;
  dragNode: TodoItemFlatNode;
  dragNodeExpandOverWaitTimeMs = 500;
  dragNodeExpandOverNode: FlatNode;
  dragNodeExpandOverTime: number;
  dragNodeExpandOverArea: DropOverAreaType;
  lastDragOverTarget: Element;
  dropOverAreaType = DropOverAreaType;

  @ViewChild('emptyItem') emptyItem: ElementRef;

  nestedNodeMap = new Map<string, TodoItemFlatNode>();
  getLevel = (node: TodoItemFlatNode) => node.level;
  isExpandable = (node: TodoItemFlatNode) => node.expandable;
  getChildren = (node: Category): Category[] => node.Children;
  hasChild = (_: number, _nodeData: FlatNode) => _nodeData.expandable;
  subscription = new Subscription();



  constructor(@Inject('IAnalyticCategoryService') private manageCategoryService: IAnalyticCategoryService,
    private dialog: MatDialog,
    @Inject('Database') private database: TreeListMenuActionsService,
    private translateService: TranslateService,
    private snackbarService: SnackBarService,
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.configureTree();
    await this.loadCategories();
  }

  private configureTree(): void
  {
    this.treeFlattener = new MatTreeFlattener(this.transformer, this.getLevel, this.isExpandable, this.getChildren);
    this.treeControl = new FlatTreeControl<TodoItemFlatNode>(this.getLevel, this.isExpandable);
    this.dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
    this.subscription.add(this.database.dataChange.subscribe(data =>
    {
      this.dataSource.data = [];
      this.dataSource.data = data;
    }));
  }

  transformer = (node: Category, level: number) =>
  {
    const existingNode = this.nestedNodeMap.get(node.CategoryUID);
    const flatNode = existingNode && existingNode.CategoryUID === node.CategoryUID
      ? existingNode
      : new TodoItemFlatNode();
    flatNode.Name = node.Name;
    flatNode.level = level;
    flatNode.ParentUID = node.ParentUID;
    flatNode.CategoryUID = node.CategoryUID;

    flatNode.expandable = (node.Children && node.Children.length > 0);
    this.database.setFlatMapCategory(flatNode.CategoryUID, node);
    this.nestedNodeMap.set(node.CategoryUID, flatNode);
    return flatNode;
  };

  async loadCategories(): Promise<void>
  {
    this.isLoading = true;

    try
    {
      const res = await firstValueFrom(this.manageCategoryService.getCategoryWithRoles());

      this.database.calculateHierarchy(res.Categories);

      this.staffRoles = res.ApplicableRoles;
      this.database.initialize(res.Categories);
    } catch (error)
    {
      console.error("Error loading categories:", error);
    } finally
    {
      this.isLoading = false;
    }
  }


  onContextMenu(event: MouseEvent, category: Category): void
  {
    event.preventDefault();
    this.selectedCategory = category;
    this.contextMenuTrigger.openMenu();
  }

  async deleteCategory(node: TodoItemFlatNode): Promise<void>
  {
    const dialogRef = this.dialog.open(DeleteCategoryDialogComponent, {
      width: '450px',
      data: { category: node }
    });

    const result = await firstValueFrom(dialogRef.afterClosed());

    if (result)
    {
      this.deleteItem(node);
    }

  }

  async deleteItem(node: TodoItemFlatNode): Promise<void>
  {
    this.isLoading = true;
    const request = new DeleteCategoryRequest();
    request.CategoryUID = node?.CategoryUID;

    try
    {
      await firstValueFrom(this.manageCategoryService.deleteCategory(request));

      await this.loadCategories();

      this.snackbarService.showSuccess(
        this.translateService.instant('ANALYTICS.SECURITY.MANAGE_CATEGORY.CATEGORY_DELETED'),
        "",
        null,
        "left",
        "bottom"
      );
    } catch (error)
    {
      console.error("Error deleting category:", error);
    } finally
    {
      this.isLoading = false;
    }
  }

  async manageCategory(node: TodoItemFlatNode): Promise<void>
  {
    const selectedCategory = this.database.getCategoryByUID(node.CategoryUID);
    if (!selectedCategory) return;

    const dialogRef = this.dialog.open(ManageCategoryDialogComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      width: '450px',
      data: {
        title: "Manage Category",
        category: selectedCategory,
        categories: this.database.getApplicableCategories(node.CategoryUID),
        staffRoles: this.staffRoles
      } as CategoryDialogData
    });

    const result = await firstValueFrom(dialogRef.afterClosed());

    if (!result) return;

    this.isLoading = true;
    const request = new PostDetailedCategoryRequest();
    // Find the selected parent category to get its CategoryUID

    request.ParentUID = result.category.ParentUID || null;
    request.Name = result.category.Name;
    request.CategoryUID = result.category.CategoryUID;
    request.RoleUIDs = result?.category?.Roles || [];
    request.Identifier = result?.category?.Identifier;
    if (request.ParentUID === request.CategoryUID)
    {
      request.ParentUID = null;
    }

    try
    {
      await firstValueFrom(this.manageCategoryService.postCategory(request));

      this.snackbarService.showSuccess(
        this.translateService.instant('ANALYTICS.SECURITY.MANAGE_CATEGORY.CATEGORY_SAVED'),
        "",
        null,
        "left",
        "bottom"
      );

      await this.loadCategories();
    } catch (error)
    {
      console.error("Error managing category:", error);
    } finally
    {
      this.isLoading = false;
    }
  }


  async addCategory(): Promise<void>
  {
    const dialogRef = this.dialog.open(ManageCategoryDialogComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      width: '450px',
      data: {
        title: "Add Category",
        category: new Category(),
        categories: this.database.getApplicableCategories(),
        staffRoles: this.staffRoles
      }
    });

    const result = await firstValueFrom(dialogRef.afterClosed());

    if (!result) return;

    this.isLoading = true;
    const request = new PostDetailedCategoryRequest();
    request.ParentUID = result.category.ParentUID;
    request.Name = result.category.Name;
    request.RoleUIDs = result?.category?.Roles || [];
    request.Identifier = result?.category?.Identifier;
    request.CategoryUID = '';

    try
    {
      await firstValueFrom(this.manageCategoryService.postCategory(request));

      this.snackbarService.showSuccess(
        this.translateService.instant('ANALYTICS.SECURITY.MANAGE_CATEGORY.CATEGORY_SAVED'),
        "",
        null,
        "left",
        "bottom"
      );

      await this.loadCategories();
    } catch (error)
    {
      console.error("Error adding category:", error);
    } finally
    {
      this.isLoading = false;
    }
  }

  onDrop(event: any): void
  {
    const prevIndex = this.dataSource.data.findIndex(d => d === event.item.data);
    const newIndex = event.currentIndex;
    if (prevIndex !== newIndex)
    {
      const movedCategory = this.dataSource.data.splice(prevIndex, 1)[0];
      this.dataSource.data.splice(newIndex, 0, movedCategory);
      this.dataSource.data = [...this.dataSource.data];
    }
  }

  handleDragStart(event, node)
  {
    if (event.dataTransfer)
      event.dataTransfer.setDragImage(this.emptyItem.nativeElement, 0, 0);
    this.dragNode = node;
    this.treeControl.collapse(node);

    const targetElement = event.target as HTMLElement;
    targetElement.classList.add('dragged');
  }

  handleDragOver(event, node: FlatNode)
  {
    event.preventDefault();
    if (node !== this.dragNodeExpandOverNode)
    {
      this.dragNodeExpandOverNode = node;
      this.dragNodeExpandOverTime = new Date().getTime();
    }

    let isValidToBeBelow = false;
    let currentNode = this.database.getCategoryByUID(node.CategoryUID);
    let parent = this.database.getParentFromNodes(currentNode);
    if (parent == null)
    {
      let items = this.database.data;
      isValidToBeBelow = items.indexOf(currentNode) == items.length - 1;
    } else
    {
      isValidToBeBelow = parent.Children.indexOf(currentNode) == parent.Children.length - 1;
    }

    // Handle drag area
    const target = event.currentTarget || event.target;
    let percentageY = event.offsetY / target.clientHeight;
    let centerThreshold = 0.75;
    if (!isValidToBeBelow)
      centerThreshold = 1.0;

    if (percentageY < 0.25)
    {
      this.dragNodeExpandOverArea = DropOverAreaType.Above;
    }
    else if (percentageY >= 0.25 && percentageY <= centerThreshold)
    {
      this.dragNodeExpandOverArea = DropOverAreaType.Center;
    }
    else if (percentageY > 0.75 && percentageY <= 1 && isValidToBeBelow)
    {
      this.dragNodeExpandOverArea = DropOverAreaType.Below;
    }
    else
    {
      this.dragNodeExpandOverArea = null;
    }
  }

  async handleDrop(event: any, node: TodoItemFlatNode): Promise<void>
  {

    if (node === this.dragNode || this.dragNode === null || this.dragNode === undefined)
    {
      this.resetDragState();
      return;
    }

    event.preventDefault();

    let request: UpdateCategorySortOrderRequest = {
      CategoryUID: this.dragNode.CategoryUID,
      ParentUID: this.dragNode.ParentUID,
      NewSortOrder: 0
    };

    let newItem: Category;

    const from = this.database.getCategoryByUID(this.dragNode.CategoryUID);
    const to = this.database.getCategoryByUID(node.CategoryUID);

    // Determine the target parent and its level for validation
    let targetParentLevel = 0;

    if (this.dragNodeExpandOverArea === DropOverAreaType.Above || this.dragNodeExpandOverArea === DropOverAreaType.Below)
    {
      // When dropping above or below, the dragged item becomes a sibling of the target node
      targetParentLevel = to.Level;
    }
    else if (this.dragNodeExpandOverArea === DropOverAreaType.Center)
    {
      // When dropping in the center, the dragged item becomes a child of the target node
      targetParentLevel = to.Level;
    }

    // Validate if the drop would exceed 4 levels
    if (this.database.wouldExceedMaxLevels(from, targetParentLevel, 4))
    {
      this.database.initialize(JSON.parse(JSON.stringify(this.database.data)));
      this.snackbarService.showError(
        this.translateService.instant('ANALYTICS.SECURITY.MANAGE_CATEGORY.CATEGORIES_CAN_ONLY_BE_NESTED_UP_TO_4_LEVELS'),
        "",
        null,
        "left",
        "bottom"
      );
      this.resetDragState();
      return;
    }

    if (this.dragNodeExpandOverArea === DropOverAreaType.Above)
    {
      request.ParentUID = node.ParentUID;
      newItem = this.database.copyPasteItemAbove(from, to);
    }
    else if (this.dragNodeExpandOverArea === DropOverAreaType.Below)
    {
      request.ParentUID = to.ParentUID;
      newItem = this.database.copyPasteItemBelow(from, to);
    }
    else
    {
      request.ParentUID = to.CategoryUID;
      newItem = this.database.copyPasteItem(from, to);
    }

    this.dragNodeExpandOverArea = null;

    var newSortPlace = this.database.reviseSortOrderAndGetSortNumber(newItem);
    request.NewSortOrder = newSortPlace;
    try
    {
      this.isLoading = true;
      await firstValueFrom(this.manageCategoryService.postReOrder(request));

    } catch (error)
    {
      console.error(error);
      this.resetDragState();
    } finally
    {
      this.isLoading = false;
      //this.database.initialize(JSON.parse(JSON.stringify(this.database.data)));
      await this.loadCategories();
    }

  }

  handleDragEnd(event: DragEvent): void
  {
    const targetElement = event.target as HTMLElement;
    targetElement.classList.remove('dragged');
    if (event.dataTransfer.dropEffect == "none")
      this.resetDragState();
  }

  resetDragState()
  {
    this.dragNode = null;
    this.dragNodeExpandOverNode = null;
    this.dragNodeExpandOverArea = null;
    this.dragNodeExpandOverTime = 0;
  }

  handleTouchStart(event: TouchEvent, node: TodoItemFlatNode): void
  {
    if (this.checkClosestIsActionButton(event))
    {
      return;
    }
    event.preventDefault();

    const touch = event.touches[0];
    const targetElement = document.elementFromPoint(touch.clientX, touch.clientY);

    if (targetElement)
    {
      const dragStartEvent = new CustomEvent('dragstart', {
        bubbles: true,
        detail: { node },
      });
      targetElement.dispatchEvent(dragStartEvent);
    }

    this.lastDragOverTarget = null;
  }

  checkClosestIsActionButton(event: TouchEvent | DragEvent)
  {
    return (
      (event.target as HTMLElement).closest('.a-tree-node-item-expand-toggle') ||
      (event.target as HTMLElement).closest('.a-more-button') ||
      (event.target as HTMLElement).closest('.a-manage-page') ||
      (event.target as HTMLElement).closest('.a-remove-page')
    );
  }

  handleTouchMove(event: TouchEvent, node: FlatNode): void
  {
    if (this.checkClosestIsActionButton(event))
    {
      return;
    }
    event.preventDefault();

    const touch = event.touches[0];
    const targetElement = document.elementFromPoint(touch.clientX, touch.clientY);

    if (targetElement)
    {
      const boundingRect = targetElement.getBoundingClientRect();
      const offsetY = touch.clientY - boundingRect.top;

      if (this.lastDragOverTarget !== targetElement)
      {
        if (this.lastDragOverTarget)
        {
          const dragLeaveEvent = new CustomEvent('dragleave', { bubbles: true });
          this.lastDragOverTarget.dispatchEvent(dragLeaveEvent);
        }

        const dragOverEvent = new CustomEvent('dragover', { bubbles: true });
        Object.assign(dragOverEvent, { offsetY, node });
        targetElement.dispatchEvent(dragOverEvent);

        this.lastDragOverTarget = targetElement;
      }
    }
  }

  handleTouchEnd(event: TouchEvent, node: TodoItemFlatNode): void
  {
    if (this.checkClosestIsActionButton(event))
    {
      return;
    }

    const touch = event.changedTouches[0];
    const targetElement = document.elementFromPoint(touch.clientX, touch.clientY);

    if (targetElement)
    {
      const dropEvent = new CustomEvent('drop', { bubbles: true });
      Object.assign(dropEvent, { node });
      targetElement.dispatchEvent(dropEvent);
    }

    this.lastDragOverTarget = null;
  }

  ngOnDestroy(): void
  {
    this.subscription.unsubscribe();
  }

}
