import { Component, Inject, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb, CardDialogComponent, SnackBarService } from '@tei/material';
import { IReportGeneratorService } from '../../../../@intermediate/models/reports/reports-generator.interface';
import { ResponsiveService } from '@tei/core';
import
{
  ComponentTypeEnum,
  GetReportGeneratorParamsRequest,
  ReportDynamicForm,
  DayOfWeek,
  Month,
  NotificationType,
  PostSaveAnalyticsObjectReportRequest,
  ReportDynamicFormPost,
  DateRange,
  FieldValue,
  Users,
  PostReportGeneratorResponse
} from '../../../../@intermediate/models/reports/reports-generator.model';
import { firstValueFrom, Subscription } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ScheduleComponent } from '../../../../components/report-generator/schedule/schedule.component';
import { NotficationComponent } from '../../../../components/report-generator/notfication/notfication.component';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { DuplicateReportDialogComponent } from '../../../../components/report-generator/duplicate-report-dialog/duplicate-report-dialog.component';



@Component({
  selector: 'tei-analytics-group-analytics-report-generator',
  templateUrl: './report-generator.component.html',
  styleUrls: ['./report-generator.component.scss']
})
export class ReportGeneratorComponent implements OnInit, OnDestroy
{
  @ViewChild(ScheduleComponent) private readonly scheduleComponent!: ScheduleComponent;
  @ViewChild(NotficationComponent) private readonly notficationComponent!: NotficationComponent;

  private readonly request = new GetReportGeneratorParamsRequest();


  readonly breadCrumbData: BreadCrumb[] = this.initializeBreadcrumbs();
  readonly ComponentTypeEnum = ComponentTypeEnum;

  reportForm: FormGroup = this.initializeEmptyForm();
  responseReportGenerator: ReportDynamicForm[] = [];
  subscription = new Subscription();

  isSmallScreen = false;
  isLoading = false;
  isFormReady = false;
  isSubmitting = false;
  selectedReportID: string | null = null;

  constructor(
    private translate: TranslateService,
    @Inject('IReportGeneratorService') private reportGeneratorService: IReportGeneratorService,
    private responsiveService: ResponsiveService,
    private route: ActivatedRoute,
    private router: Router,
    private fb: FormBuilder,
    private dialog: MatDialog,
    private snackBar: SnackBarService
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.selectedReportID = this.route.snapshot.paramMap.get('id');
    this.setupScreenSizeDetection();
    await this.loadInitialData();
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }

  onScheduleFormReady(form: FormGroup): void
  {
    if (this.isFormReady)
    {
      this.reportForm.setControl('schedule', form);
    }
  }

  onScheduleFormValueChange(value: any): void
  {
    if (this.isFormReady && this.reportForm && this.scheduleComponent?.scheduleForm.valid)
    {
      const scheduleValue = {
        EffectiveTime: value.generateOn === 'later' ? value.laterDateTime : null,
        JobScheduleModel: value.generateOn === 'recurring' ? value.JobScheduleModel : null
      };
      this.reportForm.patchValue({ schedule: scheduleValue }, { emitEvent: false });
    }
  }

  onNotificationFormReady(form: FormGroup): void
  {
    if (this.isFormReady)
    {
      this.reportForm.setControl('notification', form);
    }
  }

  onNotificationFormValueChange(value: any): void
  {
    if (this.isFormReady && this.reportForm && this.notficationComponent?.notificationForm.valid)
    {
      this.reportForm.patchValue({ notification: value }, { emitEvent: false });
    }
  }

  submitForm(): void
  {
    if (!this.reportForm.valid || this.isSubmitting)
    {
      this.markFormAsTouched();
      return;
    }

    this.isSubmitting = true;
    const scheduleValue = this.scheduleComponent?.getFormValue();
    const formData = this.createFormData(scheduleValue);
    this.submitReportData(formData);
  }

  private initializeBreadcrumbs(): BreadCrumb[]
  {
    return [
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
        relativeUrl: ''
      },
      {
        title: this.translate.instant('ANALYTICS.REPORTS.REPORTS_NAV.NEW_REPORTS'),
        relativeUrl: '../../new-reports/'
      },
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.REPORTS'),
        relativeUrl: '../'
      },
      {
        title: this.translate.instant('ANALYTICS.REPORTS.REPORTS_NAV.REPORTS_GENERATOR'),
        relativeUrl: ''
      }
    ];
  }

  private initializeEmptyForm(): FormGroup
  {
    return this.fb.group({
      parameters: this.fb.group({}),
      schedule: this.fb.group({}),
      notification: this.fb.group({})
    });
  }

  private setupScreenSizeDetection(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isSmallScreen = ['xs', 'sm', 'md'].includes(screenSize);
    }));
  }

  private async loadInitialData(): Promise<void>
  {
    try
    {
      this.isLoading = true;
      this.request.AnalyticsObjectUID = this.selectedReportID;

      const response = await firstValueFrom(
        this.reportGeneratorService.getParams(this.request)
      );

      if (response?.ReportParameters?.length)
      {
        // Filter out any null or invalid report parameters
        this.responseReportGenerator = response.ReportParameters.filter(report =>
          report && report.Fields && Array.isArray(report.Fields)
        );

        if (this.responseReportGenerator.length)
        {
          await this.initializeForm();
          this.isFormReady = true;
        } else
        {
          console.error('No valid report parameters found in the response');
          this.isFormReady = false;
        }
      } else
      {
        console.error('No report parameters found in the response');
        this.isFormReady = false;
      }
    } catch (error)
    {
      console.error('Error loading initial data:', error);
      this.isFormReady = false;
      this.snackBar.showError(
        this.translate.instant('ANALYTICS.REPORT_GENERATOR.LOAD_DATA_ERROR')
      );
    } finally
    {
      this.isLoading = false;
    }
  }

  private async initializeForm(): Promise<void>
  {
    const formControls = this.createFormControls();
    const parametersGroup = this.fb.group(formControls);
    this.reportForm.setControl('parameters', parametersGroup);
  }

  private createFormControls(): { [key: string]: FormControl | FormGroup; }
  {
    const controls: { [key: string]: FormControl | FormGroup; } = {};

    this.responseReportGenerator.forEach(form =>
    {
      if (!form?.Fields) return;

      form.Fields.forEach(field =>
      {
        if (!field) return;

        if (field.ComponentType === ComponentTypeEnum.RangeDatePicker)
        {
          const dateRange = field.SelectedValue as DateRange;
          controls[field.FieldID] = new FormGroup({
            start: new FormControl(dateRange?.start ? new Date(dateRange.start) : null),
            end: new FormControl(dateRange?.end ? new Date(dateRange.end) : null)
          });
        } else if (field.ComponentType === ComponentTypeEnum.Lookup)
        {
          controls[field.FieldID + '_displayName'] = new FormControl(field.SelectedValue ? field.SelectedValue.split(',').join(', ') : '');
          controls[field.FieldID] = new FormControl(field.SelectedValue ? field.SelectedValue.split(',') : '');
        } else
        {
          controls[field.FieldID] = new FormControl(field.SelectedValue !== null && field.SelectedValue !== undefined ? field.SelectedValue : '');
        }
      });
    });

    return controls;
  }

  private isDateRange(value: any): value is DateRange
  {
    return value &&
      typeof value === 'object' &&
      'start' in value &&
      'end' in value;
  }

  private getNotificationEmails(): string[]
  {
    return this.reportForm.get('notification.NotificationType')?.value === NotificationType.Email
      ? this.reportForm.get('notification.emailList')?.value || []
      : [];
  }

  private getNotificationUserUIDs(): string[]
  {
    return this.reportForm.get('notification.NotificationType')?.value === NotificationType.InApp
      ? this.reportForm.get('notification.selectedStaffUsers')?.value || []
      : [];
  }

  private formatDateToISOString(date: Date | string | null): string | null
  {
    if (!date) return null;
    const d = new Date(date);
    // Use local date methods instead of UTC
    const year = d.getFullYear();
    const month = String(d.getMonth() + 1).padStart(2, '0');
    const day = String(d.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  }

  private createFormData(scheduleValue: any): PostSaveAnalyticsObjectReportRequest
  {
    const formattedParameters: ReportDynamicFormPost[] = this.responseReportGenerator.map(report =>
    {
      return {
        FormName: report.FormName,
        FormID: report.FormID,
        Fields: report.Fields.filter(field => field !== null).map(field => {
          let formValue = this.reportForm.get('parameters')?.get(field.FieldID)?.value;

          let selectedValue: FieldValue = null;
          let selectedRangeValue: DateRange | null = null;

          if (field.ComponentType === ComponentTypeEnum.RangeDatePicker && this.isDateRange(formValue))
          {
            selectedRangeValue = {
              start: this.formatDateToISOString(formValue.start),
              end: this.formatDateToISOString(formValue.end)
            };
          } else if (field.ComponentType === ComponentTypeEnum.DatePicker && formValue) {
            selectedValue = this.formatDateToISOString(formValue);
          } else if (field.ComponentType === ComponentTypeEnum.NumberInput && formValue !== null && formValue !== undefined)
          {
            selectedValue = Number(formValue);
          } else if (field.ComponentType === ComponentTypeEnum.Lookup) {
            // For lookup fields, ensure we get the actual value
            if (formValue) {
              selectedValue = formValue.toString();
            } else {
              // If no value in the main control, try to get it from the displayName control
              const displayValue = this.reportForm.get('parameters')?.get(field.FieldID + '_displayName')?.value;
              if (displayValue) {
                selectedValue = displayValue.toString();
              }
            }
          } else if (field.ComponentType === ComponentTypeEnum.TextBox) {
            selectedValue = formValue ? formValue.toString() : null;
          } else if (!this.isDateRange(formValue)) {
            selectedValue = formValue as FieldValue;
          }

          return {
            FieldID: field.FieldID,
            SelectedValue: selectedValue,
            SelectedRangeValue: selectedRangeValue,
            ParamIndex: field.ParamIndex,
            ParamName: field.ParamName,
            StepNum: field.StepNum
          };
        })
      };
    });

    return {
      AnalyticsObjectUID: this.selectedReportID,
      Parameters: formattedParameters,
      JobScheduleModel: this.createJobScheduleModel(scheduleValue),
      NotificationMethod: this.reportForm.get('notification.NotificationType')?.value,
      NotificationEmailAddresses: this.getNotificationEmails(),
      NotificationUserUIDs: this.getNotificationUserUIDs(),
      EffectiveTime: scheduleValue?.generateOn === 'later' ? this.formatDateToISOString(scheduleValue.laterDateTime) : null,
      CheckValidity: true
    };
  }

  private createJobScheduleModel(scheduleValue: any): any
  {
    if (scheduleValue?.generateOn !== 'recurring') return null;

    return {
      StartDate: this.formatDateToISOString(scheduleValue.startDate),
      EndDate: scheduleValue.endCondition === 'endBy' ? this.formatDateToISOString(scheduleValue.endDateRange) : null,
      EndAfterOccurrences: scheduleValue.endCondition === 'endAfter' ? scheduleValue.endAfterOccurrences : null,
      Daily: this.createDailySchedule(scheduleValue),
      Weekly: this.createWeeklySchedule(scheduleValue),
      Monthly: this.createMonthlySchedule(scheduleValue),
      Yearly: this.createYearlySchedule(scheduleValue)
    };
  }

  private createDailySchedule(scheduleValue: any): any
  {
    if (scheduleValue.recurrencePattern !== 'daily') return null;
    return {
      dailyIntervalRepeat: scheduleValue.dailyOption === 'everyWeekday' ? 1 : scheduleValue.dailyDays
    };
  }

  private createWeeklySchedule(scheduleValue: any): any
  {
    if (scheduleValue.recurrencePattern !== 'weekly') return null;

    const selectedDays = [
      scheduleValue.monday ? DayOfWeek.Monday : null,
      scheduleValue.tuesday ? DayOfWeek.Tuesday : null,
      scheduleValue.wednesday ? DayOfWeek.Wednesday : null,
      scheduleValue.thursday ? DayOfWeek.Thursday : null,
      scheduleValue.friday ? DayOfWeek.Friday : null,
      scheduleValue.saturday ? DayOfWeek.Saturday : null,
      scheduleValue.sunday ? DayOfWeek.Sunday : null
    ].filter(Boolean);

    return {
      weeklyIntervalRepeat: scheduleValue.weeklyRecurEvery,
      dayOfWeek: selectedDays
    };
  }

  private createMonthlySchedule(scheduleValue: any): any
  {
    if (scheduleValue.recurrencePattern !== 'monthly') return null;

    return {
      dayOfMonth: scheduleValue.monthlyOption === 'Day' ? scheduleValue.numberOfDays : null,
      monthlyIntervalRepeat: scheduleValue.numberOfMonths,
      weekNumber: scheduleValue.monthlyOption === 'weekly' ? this.getWeekNumber(scheduleValue.dayValue) : null,
      dayOfWeek: scheduleValue.monthlyOption === 'weekly' ? [this.getDayOfWeek(scheduleValue.weekDay)] : []
    };
  }

  private createYearlySchedule(scheduleValue: any): any
  {
    if (scheduleValue.recurrencePattern !== 'yearly') return null;

    return scheduleValue.yearlyOption === 'onDay'
      ? {
        yearlyIntervalRepeat: scheduleValue.yearlyRecurEvery,
        monthOfYear: Month[scheduleValue.yearlyMonth as keyof typeof Month],
        dayOfMonth: scheduleValue.yearlyDay,
        weekNumber: null,
        dayOfWeek: []
      }
      : {
        yearlyIntervalRepeat: scheduleValue.yearlyRecurEvery,
        monthOfYear: Month[scheduleValue.yearlyMonthForWeekday as keyof typeof Month],
        weekNumber: this.getWeekNumber(scheduleValue.yearlyPosition),
        dayOfWeek: [this.getDayOfWeek(scheduleValue.yearlyWeekday)],
        dayOfMonth: null
      };
  }

  private openDuplicateDialog(response: PostReportGeneratorResponse, formData: PostSaveAnalyticsObjectReportRequest): void
  {
    const dialogRef = this.dialog.open(CardDialogComponent, {
      panelClass: ['o-confirm-dialog', 'o-duplicate-report-dialog'],
      data: {
        component: DuplicateReportDialogComponent,
        dialogData: {
          title: this.translate.instant('ANALYTICS.REPORT_GENERATOR.DUPLICATE_REPORT_NOTIFICATION'),
          content: response.ErrorMessage,
          reportLink: response.ReportLink,
          closeButtonLabel: this.translate.instant('ANALYTICS.REPORT_GENERATOR.DUPLICATE_REPORT_CLOSE'),
          submitButtonLabel: this.translate.instant('ANALYTICS.REPORT_GENERATOR.DUPLICATE_REPORT_OK'),
          submitButtonColor: 'accent',
          closeButtonClass: "mat-stroked-button"
        }
      },
      width: '400px',
    });

    this.subscription.add(dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        formData.CheckValidity = false;
        this.submitReportData(formData);
      } else
      {
        this.isSubmitting = false;
      }
    }));
  }

  private markFormAsTouched(): void
  {
    Object.keys(this.reportForm.controls).forEach(key =>
    {
      const control = this.reportForm.get(key);
      control?.markAsTouched();
    });
  }

  private getWeekNumber(position: string): number
  {
    const positionOptions = ['first', 'second', 'third', 'fourth', 'last'];
    return positionOptions.indexOf(position) + 1;
  }

  private getDayOfWeek(day: string): DayOfWeek
  {
    return DayOfWeek[day as keyof typeof DayOfWeek];
  }

  private async submitReportData(formData: PostSaveAnalyticsObjectReportRequest): Promise<void>
  {
    try
    {

      const response = await firstValueFrom(
        this.reportGeneratorService.saveReport(formData)
      );


      if (!response?.ReportLink && !response?.ErrorMessage)
      {
        // Show success message
        this.snackBar.showSuccess(
          this.translate.instant('ANALYTICS.REPORT_GENERATOR.REPORT_CREATED_SUCCESS'),
          '',
          null,
          'left'
        );

        // Navigate to my-reports
        await this.router.navigate(['../../my-reports'], { relativeTo: this.route });
      } else
      {
        // Handle error message case
        this.openDuplicateDialog(response, formData);
      }
    } catch (error)
    {
      console.error('Error submitting report:', error);

    } finally
    {
      this.isSubmitting = false;
    }
  }
}
