import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';

@Component({
  selector: 'tei-analytics-group-analytics-security',
  templateUrl: './security.component.html',
  styleUrls: ['./security.component.scss']
})
export class SecurityComponent
{

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
      relativeUrl: ''
    },
    {
      title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.SETTINGS'),
      relativeUrl: '../'
    },
    {
      title: this.translate.instant('ANALYTICS.SETTINGS.SECURITY'),
      relativeUrl: ''
    }
  ];

  constructor(private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
  ) { }


  openManageCategory() {
    this.router.navigate(['manage-category'], { relativeTo: this.activatedRoute });
  }
}
