import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { Subscription } from 'rxjs';
import { ResponsiveService } from '@tei/core';
import { DuplicateReportDialogDataModel } from '../../../@intermediate/models/reports/reports-generator.model';


@Component({
  selector: 'app-duplicate-report-dialog',
  templateUrl: './duplicate-report-dialog.component.html',
  styleUrls: ['./duplicate-report-dialog.component.scss']
})
export class DuplicateReportDialogComponent implements OnInit, OnDestroy {
  isMobile = false;
  subscription = new Subscription();
  dialogConfirmationData: DuplicateReportDialogDataModel = {
    title: 'MATERIAL.NOTES.DELETE_LABEL',
    content: 'MATERIAL.NOTES.DELETE_CONFIRMATION_MESSAGE',
    reportLink:'',
    submitButtonLabel: 'MATERIAL.NOTES.DELETE_LABEL',
    closeButtonLabel: 'MATERIAL.NOTES.CANCEL_LABEL',
    submitButtonColor: 'warn',
    closeButtonClass: "mat-stroked-button"
  };
  constructor(

    public dialog: MatDialog,
    private responsiveService: ResponsiveService,
    @Inject(MAT_DIALOG_DATA) public data
  ) {}

  ngOnInit()
  {
    this.dialogConfirmationData = this.data.dialogData;
    if (!this.dialogConfirmationData.closeButtonClass)
    {
      this.dialogConfirmationData.closeButtonClass = "mat-raised-button";
    }
    this.getScreenSize();
  }

  getScreenSize(): void
  {
    this.subscription.add(this.responsiveService.mediaBreakpoint.subscribe(screenSize =>
    {
      this.isMobile = screenSize == 'xs' || screenSize == 'sm';
    }));
  }
  ngOnDestroy() {
    this.subscription?.unsubscribe();
  }
}
