import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ResponsiveService } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { ApplicableRole, Category, CategoryDialogData, ICategoryForm } from '../../@intermediate/models/category/analytic-category.model';

@Component({
  selector: 'tei-analytics-group-analytics-manage-category-dialog',
  templateUrl: './manage-category-dialog.component.html',
  styleUrls: ['./manage-category-dialog.component.scss']
})
export class ManageCategoryDialogComponent implements OnInit
{
  isMobile = false;
  title = '';
  categories: Category[] = [];
  staffRoles: ApplicableRole[] = [];
  selectedRoleID = null;
  form = new FormGroup<ICategoryForm>({
    CategoryUID: new FormControl(''),
    CategoryID: new FormControl(0),
    ParentID: new FormControl(null),
    ParentUID: new FormControl(''),
    Name: new FormControl('', [Validators.required]),
    SortOrder: new FormControl(0),
    Roles: new FormControl([], [Validators.required]),
    Level: new FormControl(0),
    Identifier: new FormControl('', [Validators.required]),
    MaxChildDepth: new FormControl(null),
  }) as FormGroup<ICategoryForm>;

  get formControls()
  {
    return this.form?.controls;
  }

  constructor(public dialogRef: MatDialogRef<ManageCategoryDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: CategoryDialogData,
    private responsiveService: ResponsiveService,
    private snackBarService: SnackBarService)
  {
  }

  ngOnInit(): void
  {
    this.getMobileSize();
    if (this.data)
    {
      this.title = this.data.title || '';
      this.form.patchValue(this.data.category);
      this.categories = this.data?.categories || [];
      this.staffRoles = this.data?.staffRoles || [];
    }
  }

  confirm()
  {
    if (this.form.invalid)
    {
      return;
    }

    const parentUID = this.form.get('ParentUID')?.value;

    // Check if creating/editing this category would exceed 4 levels
    if (this.wouldExceedMaxLevels(parentUID))
    {
      this.snackBarService.showError('Categories can only be nested up to 4 levels.', '', null, 'left', 'bottom');
      return;
    }

    this.dialogRef.close({ category: this.form.getRawValue() });
  }

  onCancel(): void
  {
    this.dialogRef.close();
  }

  closeDialog(): void
  {
    this.dialogRef.close(false);
  }

  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isMobile = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
  }

  // Method to calculate the level of a category (using the Level property from flat list)
  private calculateCategoryLevel(categoryUID: string): number
  {
    const category = this.data.categories.find(cat => cat.CategoryUID === categoryUID);
    return category ? category.Level : -1;
  }

  // Method to check if creating/editing a category under the selected parent would exceed 4 levels
  wouldExceedMaxLevels(parentUID: string): boolean
  {
    if (!parentUID) return false; // Root level is level 0

    const parentLevel = this.calculateCategoryLevel(parentUID);
    if (parentLevel === -1) return false; // Parent not found

    // If parent is level 3, adding a child would make it level 4 (exceeds limit)
    return parentLevel >= 3;
  }

  // Method to get all available parent categories (already filtered by getApplicableCategories)
  getAvailableParentCategories(): Category[]
  {
    // The categories are already filtered by getApplicableCategories() in the main component
    // which excludes level 3+ categories and current category's descendants
    const categories = this.data.categories || [];

    // Return original categories without modification
    return categories;
  }

  // Method to get display name with hierarchy for a category
  getCategoryDisplayName(category: Category): string
  {
    return this.generateHierarchyName(category);
  }

  // Helper method to generate hierarchy name with dots for indentation
  private generateHierarchyName(category: Category): string
  {
    const dots = '.'.repeat(category.Level);
    const categoryName = category.Name || '';
    const maxLength = 64; // Maximum length before truncation

    // Use the truncation helper for consistent text handling
    const truncatedName = this.truncateText(categoryName, maxLength);

    return `${ dots }${ truncatedName }`;
  }

  // Helper method to truncate text with ellipsis
  private truncateText(text: string, maxLength: number = 64): string
  {
    if (!text || typeof text !== 'string')
    {
      return '';
    }

    if (text.length <= maxLength)
    {
      return text;
    }

    return text.substring(0, maxLength - 3) + '...';
  }

}
