import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { MyReportsService } from './@services/reports/my-report/my-reports.service';
import { AnalyticsGroupRoutingModule } from './analytics-group-routing.module';
import { AnalyticsGroupSettingModule } from './analytics-group-setting.module';
import { AnalyticsGroupComponent } from './analytics-group.component';
import { MyReportsDetailsModule } from './components/my-reports-details/my-reports-details.module';
import { AnalyticsReportAccessModule } from './pages/analytics/analytics-report-access/analytics-report-access.module';
import { AnalyticsCategoryModule } from './pages/analytics/category/analytics-category.module';
import { DashboardsModule } from './pages/analytics/dashboards/dashboards.module';
import { MyReportModule } from './pages/analytics/reports/my-report/my-report.module';
import { NewReportsModule } from './pages/analytics/reports/new-reports/new-reports.module';
import { ReportGeneratorModule } from './pages/analytics/reports/report-generator/report-generator.module';
import { ReportsNavigationModule } from './pages/analytics/reports/reports-navigation/reports-navigation.module';
import { ReportsModule } from './pages/analytics/reports/reports.module';
import { DataViewModule } from './pages/analytics/reports/view/data-view/data-view.module';
import { PdfViewModule } from './pages/analytics/reports/view/pdf-view/pdf-view.module';
import { GenerationLogModule } from './pages/analytics/settings/generation-log/generation-log.module';
import { SecurityModule } from './pages/analytics/settings/security/security.module';
import { SettingsNavigationModule } from './pages/analytics/settings/settings-navigation/settings-navigation.module';
import { SettingsModule } from './pages/analytics/settings/settings.module';
import { DefaultModule } from './pages/default/default.module';
import { PagesModule } from './pages/pages.module';
import { PipesModule } from './pipes/pipes.module';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MODULES = [
  AnalyticsGroupSettingModule,
  AnalyticsGroupRoutingModule,
  PagesModule,
  DefaultModule,
  DashboardsModule,
  SettingsModule,
  SecurityModule,
  SettingsNavigationModule,
  AnalyticsReportAccessModule,
  ReportsModule,
  ReportsNavigationModule,
  MyReportsDetailsModule,
  NewReportsModule,
  MyReportModule,
  AnalyticsCategoryModule,
  ReportGeneratorModule,
  GenerationLogModule,
  PdfViewModule,
  DataViewModule,
  PipesModule
];

const COMPONENTS = [AnalyticsGroupComponent];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES],
  declarations: [...COMPONENTS],
  providers: [
    { provide: 'IAnalyticsMyReportsService', useClass: MyReportsService }
  ]
})
export class AnalyticsGroupModule
{ }
