import { Observable, map } from "rxjs";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { IAnalyticsMyReportsService } from "../../../@intermediate/models/reports/my-reports.interface";
import { GetMyReportsRequest, GetMyReportsResponse, GetMyConfigResponse, GetGeneratedReportListLogResponse, GetGeneratedReportListLogRequest, GetGeneratedReportLogRequest, GetGeneratedReportLogResponse, BaseReportResponse, CancelReportResponse } from "../../../@intermediate/models/reports/new-reports.model";

@Injectable()
export class MyReportsService implements IAnalyticsMyReportsService
{
  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Report';
  }
  getGetGeneratedReportList(request: GetGeneratedReportListLogRequest): Observable<GetGeneratedReportListLogResponse>
  {
    const apiUrl = this.serviceUrl + '/GetGeneratedReports';
    return this.http.post<GetGeneratedReportListLogResponse>(apiUrl, request).pipe(
      map(response =>
      {
        (response as any).AnalyticsObjects = response.AnalyticsQueues || [];
        return response;
      })
    );
  }
  getMyReportListData(request: GetMyReportsRequest): Observable<GetMyReportsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetMyReports';
    return this.http.post<GetMyReportsResponse>(apiUrl, request).pipe(
      map(response =>
      {
        (response as any).AnalyticsObjects = response.AnalyticsQueues || [];
        return response;
      })
    );
  }
  getConfig(): Observable<GetMyConfigResponse>
  {
    const apiUrl = this.serviceUrl + '/config';
    return this.http.get<GetMyConfigResponse>(apiUrl);
  }

  getGeneratedReportLog(request: GetGeneratedReportLogRequest): Observable<GetGeneratedReportLogResponse>
  {
    const apiUrl = this.serviceUrl + '/GeneratedReportLog';
    return this.http.get<GetGeneratedReportLogResponse>(apiUrl, { params: request as any });
  }

  getGeneratedReportLinkJSONFile(url: string): Observable<any>
  {
    return this.http.get(url);
  }

  CancelReport(AnalyticsQueueUID: String): Observable<CancelReportResponse>
  {
    const apiUrl = this.serviceUrl + '/Cancel';
    return this.http.post<CancelReportResponse>(apiUrl, { AnalyticsQueueUID });
  }
}
