import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ReportsComponent } from './reports.component';
import { ReportsNavigationComponent } from './reports-navigation/reports-navigation.component';
import { NewReportsComponent } from './new-reports/new-reports.component';
import { ReportGeneratorComponent } from './report-generator/report-generator.component';
import { MyReportComponent } from './my-report/my-report.component';
import { PdfViewComponent } from './view/pdf-view/pdf-view.component';
import { DataViewComponent } from './view/data-view/data-view.component';

const routes: Routes = [
  {
    path: '', component: ReportsComponent,
    children: [
      { path: '', component: ReportsNavigationComponent },
      { path: 'new-reports', component: NewReportsComponent },
      { path: 'my-reports', component: MyReportComponent },
      { path: 'report-generator/:id', component: ReportGeneratorComponent },
      { path: 'view/pdf/:id', component: PdfViewComponent },
      { path: 'view/data-view/:id', component: DataViewComponent },
      { path: '**', redirectTo: '' }
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ReportsRoutingModule { }
