import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatTreeModule } from '@angular/material/tree';
import { RouterModule } from "@angular/router";
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from "@tei/core-translate";
import { BreadCrumbModule, LoadingModule, SnackBarModule } from '@tei/material';
import { TreeListMenuActionsService } from '../../../@intermediate/models/category/tree-list-menu-actions.service';
import { ManageCategoryService } from '../../../@services/manage-category.service';
import { DeleteCategoryDialogComponent } from '../../../components/manage-category-dialog/delete-category-dialog/delete-category-dialog.component';
import { ManageCategoryDialogComponent } from '../../../components/manage-category-dialog/manage-category-dialog.component';
import { AnalyticsCategoryComponent } from './analytics-category.component';
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatDialogModule, MatSelectModule, MatTreeModule, DragDropModule, MatIconModule, MatButtonModule, MatFormFieldModule, MatInputModule, MatMenuModule];
const TEI_MATERIAL_MODULES = [LoadingModule, MatDividerModule, BreadCrumbModule, SnackBarModule];
const TEI_CORE_MODULES = [
  TeiTextTranslatePipeModule,
  TranslateModule
];
const COMPONENTS = [AnalyticsCategoryComponent, ManageCategoryDialogComponent, DeleteCategoryDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    RouterModule.forChild([
      { path: '', component: AnalyticsCategoryComponent },
    ])],
  providers: [
    { provide: 'Database', useClass: TreeListMenuActionsService },
    { provide: 'DatabaseTemp', useClass: TreeListMenuActionsService },
    { provide: 'IAnalyticCategoryService', useClass: ManageCategoryService }],
  exports: [...COMPONENTS]
})
export class AnalyticsCategoryModule { }
