import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { IAnalyticsLookupService } from "../../../@intermediate/models/reports/lookup.interface";
import { GetAnalyticLookupRequest, GetAnalyticLookupResponse } from "../../../@intermediate/models/reports/lookup.model";

@Injectable()
export class AnalyticsLookupService implements IAnalyticsLookupService
{
  private readonly serviceUrl: string;
  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Lookup';
  }
  
  Get(request: GetAnalyticLookupRequest): Observable<GetAnalyticLookupResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.get<GetAnalyticLookupResponse>(apiUrl, { params: request as any });
  }

}
