import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { ChipItemModel, ChipOptionModel, ChipType } from '@tei/material';
import moment from 'moment';
import { catchError, concat, debounceTime, firstValueFrom, Observable, of, Subject, switchMap } from 'rxjs';
import { IAnalyticsMyReportsService } from '../../../@intermediate/models/reports/my-reports.interface';
import { BaseReportRequest, Category, Department, Status, StatusEnum, StatusTypeEnum, Type } from '../../../@intermediate/models/reports/new-reports.model';

@Component({
  selector: 'tei-analytics-group-analytics-my-report-header',
  templateUrl: './my-report-header.component.html',
  styleUrls: ['./my-report-header.component.scss']
})
export class MyReportHeaderComponent implements OnInit
{
  form: FormGroup;
  ChipType = ChipType;
  request: BaseReportRequest = new BaseReportRequest();
  overflowHidden = false;
  minTermLength = 0;
  minDate = moment().subtract(1, 'year').toDate();


  CategoryItemsWithDisplayName = [
    {
      chipName: 'Name',
      displayName: this.translate.instant(
        'ANALYTICS.SECURITY.CATEGORY'
      ),
      bindValue: 'CategoryUID'
    }
  ];
  categoryListSubjectArray: Observable<Category[]> = of([]);
  categoryListTypeAheadInput$ = [];
  CategoryList: Category[] = [];

  typeItemsWithDisplayName: ChipItemModel[] = [
    {
      chipName: 'Label',
      displayName: this.translate.instant(
        'ANALYTICS.SECURITY.TYPE'
      ),
      bindValue: 'Value'
    } as ChipItemModel
  ];
  typeList: Type[] = [];
  typeListSubjectArray: Observable<Type[]> = of([]);
  typeListTypeAheadInput$ = [];


  departmentList: Department[] = [];
  departmentListSubjectArray: Observable<Department[]> = of([]);
  departmentListTypeAheadInput$ = [];
  departmentWithDisplayName: ChipItemModel[] = [
    { chipName: 'Name', displayName: this.translate.instant('ANALYTICS.SECURITY.DEPARTMENT'), bindValue: 'DepartmentIdentifier' } as ChipItemModel,
  ];

  StatusEnum = StatusEnum;
  StatusTypeEnum = StatusTypeEnum;

  statusList: Status[] = [];

  statusListSubjectArray: Observable<Status[]> = of([]);
  statusListTypeAheadInput$ = [];

  StatusItemsWithDisplayName: ChipItemModel[] = [
    {
      chipName: 'Label',
      displayName: this.translate.instant('ANALYTICS.REPORTS.MY_REPORTS.STATUS'),
      bindValue: 'Value',
    } as ChipItemModel
  ];


  @Output() enterFilterValueEvent = new EventEmitter<BaseReportRequest>();
  constructor(
    public manageLoadingService: ManageLoadingService,
    private formBuilder: FormBuilder,
    private translate: TranslateService,
    @Inject('IAnalyticsMyReportsService') private myReportService: IAnalyticsMyReportsService,)
  {

  }


  async ngOnInit(): Promise<void>
  {
    this.form = this.formBuilder.group({
      searchedTerm: ['']
    });
    await this.getConfig();
    this.filterDepartmentByChipItem();
    this.filterTypeListByChipItem();
    this.filterCategoryListByChipItem();
    this.filterStatusListByChipItem();
  }

  async getConfig(): Promise<void>
  {
    const response = await firstValueFrom(this.myReportService.getConfig()) as {
      Departments: Department[];
      Types: Type[];
      Categories: Category[];
      StatusTypes: Status[];
    };
    this.departmentList = response.Departments;
    this.typeList = response.Types;
    this.CategoryList = response.Categories;
    this.statusList = response.StatusTypes.map(item => ({
      Label: item.Label,
      Value: item.Value.toString()
    }));
  }

  private filterDepartmentByChipItem(): void
  {
    this.departmentListTypeAheadInput$.push(new Subject<string>());
    this.departmentListSubjectArray[0] = concat(
      of(this.departmentList),
      this.departmentListTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterDepartmentList(searchTerm as string).pipe(
            catchError(() => of(this.departmentList)),
          ))
      )
    );
  }

  filterDepartmentList(searchTerm: string): Observable<Array<Department>>
  {
    return of(this.departmentList.filter(item => item.Name.toLowerCase().includes(searchTerm)));
  }

  filterTypeListByChipItem(): void
  {
    this.typeListTypeAheadInput$.push(new Subject<string>());
    this.typeListSubjectArray[0] = concat(
      of(this.typeList),
      this.typeListTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterTypeList(searchTerm as string).pipe(
            catchError(() => of(this.typeList)),
          ))
      )
    );
  }

  filterCategoryListByChipItem(): void
  {
    this.categoryListTypeAheadInput$.push(new Subject<string>());
    this.categoryListSubjectArray[0] = concat(
      of(this.CategoryList),
      this.categoryListTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterCategoryList(searchTerm as string).pipe(
            catchError(() => of(this.CategoryList)),
          ))
      )
    );
  }

  filterCategoryList(searchTerm: string): Observable<Array<Category>>
  {
    return of(this.CategoryList.filter(item => item.Name.toLowerCase().includes(searchTerm)));
  }

  filterStatusListByChipItem(): void
  {
    this.statusListTypeAheadInput$.push(new Subject<string>);
    this.statusListSubjectArray[0] = concat(
      of(this.statusList),
      this.statusListTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterStatusList(searchTerm as string).pipe(
            catchError(() => of(this.statusList))
          )
        )
      )
    );
  }

  filterStatusList(searchTerm: string): Observable<Status[]>
  {
    return of(this.statusList.filter(item => item.Label.toLowerCase().includes(searchTerm)));
  }

  filterTypeList(searchTerm: string): Observable<Array<Type>>
  {
    return of(this.typeList.filter(item => item.Label.toLowerCase().includes(searchTerm)));
  }


  onSearchChange(event: Event): void
  {
    this.request.SearchTerm = this.form.get('searchedTerm').value;
    event?.stopPropagation();
    event?.preventDefault();
    setTimeout(() => { this.load(); }, 300);
  }


  onSearchCategoryChange(filterObjects: Array<ChipItemModel>): void
  {
    filterObjects?.forEach((chipItem: ChipItemModel) =>
    {
      this.request.CategoryUID = chipItem.selectedItem.length > 0 ? chipItem.value : null;

    });
    this.load();
  }

  onSearchStatusChange(filterObjects: Array<ChipItemModel>): void
  {
    this.request.Status = Number(filterObjects[0].value);
    this.load();
  }

  onFilterChange(filterObjects: Array<ChipItemModel>): void
  {

    filterObjects?.forEach((chipItem: ChipItemModel) =>
    {
      if (chipItem.chipName === 'Label')
      {
        this.request.AnalyticsType = chipItem.selectedItem.length > 0 ? Number(chipItem.value) : null;
      }
      if (chipItem.chipName === 'Name')
      {
        this.request.DepartmentIdentifier = chipItem.selectedItem.length > 0 ? chipItem.value : null;
      }
      this.load();
    });
  }

  onSearchNameChange(event: ChipOptionModel): void
  {
    this.request.Name = event?.firstValue;
    this.load();
  }

  onSearchIDChange(event: ChipOptionModel): void
  {
    this.request.AnalyticsQueueID = event?.firstValue;
    this.load();
  }

  onSearchReportIDChange(event: ChipOptionModel): void
  {
    this.request.ReportID = event?.firstValue;
    this.load();
  }

  onSearchTypeChange(event: ChipOptionModel): void
  {
    const selected = this.typeList.find(item => item.Label === event[0]?.selectedItem);
    this.request.AnalyticsType = selected ? Number(selected.Value) : null;
    this.load();
  }

  onSearchGeneratedByChange(event: ChipOptionModel): void
  {
    this.request.GeneratedBy = event?.firstValue;
    this.load();
  }

  onDateRangeChangeEvent(event: ChipOptionModel): void
  {
    if (event.firstValue && event.secondValue)
    {
      this.request.StartDate = moment(event.firstValue).format('YYYY-MM-DD');
      this.request.EndDate = moment(event.secondValue).format('YYYY-MM-DD');
    } else
    {
      this.request.StartDate = null;
      this.request.EndDate = null;
    }
    this.load();
  }

  load(): void
  {
    this.enterFilterValueEvent.emit(this.request);
  }
}
