import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ResponsiveService } from '@tei/core';
import { BreadCrumb, DynamicTableInputConfig } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import * as XLSX from 'xlsx';
import { IAnalyticsMyReportsService } from '../../../../../@intermediate/models/reports/my-reports.interface';
import { SnackBarService } from '@tei/material';

@Component({
  selector: 'tei-analytics-group-analytics-data-view',
  templateUrl: './data-view.component.html',
  styleUrls: ['./data-view.component.scss']
})
export class DataViewComponent implements OnInit
{
  reportUID: string;
  isLoading = true;
  breadCrumbData: BreadCrumb[] = [];
  reportData: any;
  reportCSVData: string;
  tableConfig: DynamicTableInputConfig;
  reportName: string;
  reportID: string;
  isSmallScreen = false;

  constructor(
    private snackBarService: SnackBarService,
    private route: ActivatedRoute,
    private router: Router,
    private translateService: TranslateService,
    private responsiveService: ResponsiveService,
    @Inject('IAnalyticsMyReportsService') private myReportService: IAnalyticsMyReportsService
  ) { }

  ngOnInit(): void
  {
    this.reportUID = this.route.snapshot.paramMap.get('id');
    this.initializeBreadcrumbs();
    this.loadReportData();
    this.detectScreenSize();
  }

  detectScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = ['xs', 'sm', 'md'].includes(screenSize);
  }
  private initializeBreadcrumbs(): void
  {
    const currentUrl = this.router.url;
    const isGenerationLog = currentUrl.includes('/settings/');

    if (isGenerationLog)
    {
      this.breadCrumbData = [
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
          relativeUrl: '',
        },
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.SETTINGS'),
          relativeUrl: '../../',
        },
        {
          title: this.translateService.instant('ANALYTICS.SETTINGS.GENERATION_LOG'),
          relativeUrl: '../../../generation-log',
        },
        {
          title: this.translateService.instant('ANALYTICS.REPORTS.REPORTS_NAV.DATAVIEW_REPORT_VIEWER'),
          relativeUrl: '',
        }
      ];
    } else
    {
      this.breadCrumbData = [
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
          relativeUrl: '',
        },
        {
          title: this.translateService.instant('ANALYTICS.ANALYTICS_NAVIGATION.REPORTS'),
          relativeUrl: '../../',
        },
        {
          title: this.translateService.instant('ANALYTICS.REPORTS.REPORTS_NAV.MY_REPORTS'),
          relativeUrl: '../../../my-reports',
        },
        {
          title: this.translateService.instant('ANALYTICS.REPORTS.REPORTS_NAV.DATAVIEW_REPORT_VIEWER'),
          relativeUrl: '',
        }
      ];
    }
  }

  private async loadReportData(): Promise<void>
  {
    this.isLoading = true;

    try
    {
      const response = await firstValueFrom(this.myReportService.getGeneratedReportLog({ AnalyticsQueueUID: this.reportUID }));
      this.reportName = response.Name;
      this.reportID = response.ReportID;
      this.reportData = await firstValueFrom(this.myReportService.getGeneratedReportLinkJSONFile(response.ReportLink));

      if (!this.reportData?.length)
      {
        this.snackBarService.showError(this.translateService.instant('ANALYTICS.REPORTS.DATA_VIEW.NO_DATA_FOUND'), '', null, 'left', 'bottom', 5);
        return;
      }

      this.tableConfig = {
        Data: this.reportData,
        IsMultiSelect: false,
        ShowSelectionColumn: false,
        ExportFilteredData: true,
        ShowMobileView: false,
        ShowHorizontalScrollingPagination: !this.isSmallScreen,
      };

    } catch (err)
    {
      console.error(err);
    } finally
    {
      this.isLoading = false;
    }

  }

  private getExportFilename(extension: string): string
  {
    return `report_${ this.reportName }.${ extension }`;
  }

  private validateExportData(): boolean
  {

    if (!this.reportData?.length)
    {
      console.warn('No data available to export');
      return false;
    }
    return true;
  }

  exportFilteredData(filteredData: any[]): void
  {
    this.reportData = filteredData;
  }

  exportCSVFilteredData(filteredData: string): void
  {
    this.reportCSVData = filteredData;
  }

  private downloadFile(blob: Blob, filename: string): void
  {
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');

    link.setAttribute('href', url);
    link.setAttribute('download', filename);
    link.style.visibility = 'hidden';

    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  }

  exportExcel(): void
  {
    if (!this.validateExportData()) return;

    try
    {
      const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(this.reportData);
      const workbook: XLSX.WorkBook = {
        Sheets: { 'Report Data': worksheet },
        SheetNames: ['Report Data']
      };

      const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
      const blob = new Blob([excelBuffer], {
        type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
      });

      this.downloadFile(blob, this.getExportFilename('xlsx'));
    } catch (error)
    {
      console.error('Error exporting to Excel:', error);
    }
  }

  exportCSV(): void
  {
    if (!this.validateExportData()) return;

    try
    {
      const csvContent = this.reportCSVData;
      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
      this.downloadFile(blob, this.getExportFilename('csv'));
    } catch (error)
    {
      console.error('Error exporting to CSV:', error);
    }
  }

}
