import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { GetContactSearchByIDResponse, GetContactSearchResponse } from "@tei/office-contacts";
import { Observable } from "rxjs";
import { map } from "rxjs/operators";
import { IReportGeneratorService } from "../../@intermediate/models/reports/reports-generator.interface";
import
{
  GetReportGeneratorParamsRequest,
  GetReportGeneratorParamsResponse,
  PostReportGeneratorResponse,
  PostSaveAnalyticsObjectReportRequest,
  StaffUserListResponse
} from "../../@intermediate/models/reports/reports-generator.model";

@Injectable()
export class ReportGeneratorService implements IReportGeneratorService
{
  private readonly serviceUrl: string;

  constructor(
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient
  )
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Report';
  }

  getParams(request: GetReportGeneratorParamsRequest): Observable<GetReportGeneratorParamsResponse>
  {
    const apiUrl = this.serviceUrl + '/params';
    return this.http.get<GetReportGeneratorParamsResponse>(apiUrl, { params: request as any });
  }

  saveReport(request: PostSaveAnalyticsObjectReportRequest): Observable<PostReportGeneratorResponse>
  {
    return this.http.post<PostReportGeneratorResponse>(this.serviceUrl, request);
  }

  staffUserList(params?: { ItemSearch?: string; }): Observable<StaffUserListResponse>
  {
    const apiUrl = this.serviceUrl + '/StaffUsers';
    return this.http.get<StaffUserListResponse>(apiUrl, { params: params as any });
  }

  search(searchTerm: string): Observable<GetContactSearchResponse>
  {
    return this.staffUserList({ ItemSearch: searchTerm }).pipe(
      map(response => ({
        Contacts: response.Users.map(user => ({
          ContactID: user.UserUID,
          DisplayName: user.FirstName + ' ' + user.LastName,
          Email: user.Email,
          FirstName: user.FirstName,
          LastName: user.LastName,
          Salutation: '',
          MemberID: '',
          Phone: '',
          ProfilePicture: user?.ProfilePicture,
          Address1: '',
          Address2: '',
          City: '',
          State: '',
          PostalCode: '',
          Country: '',
          GeneratedAddress: '',
          PriceLevel: '',
          MemberStatusID: '',
          MemberStatusDescription: '',
          ContactClassID: '',
          ContactClassDescription: '',
          PriceLevelID: ''
        }))
      }))
    );
  }

  getDetail(contactId: string): Observable<GetContactSearchByIDResponse>
  {
    return;
  }
}
