import { Component, Input } from '@angular/core';
import { SummarySection, SummarySectionMetadata, getTrendIconName, getTrendIconColor, TrendIconType, SummaryMetricDisplay, SummaryMetricType } from '../../../@intermediate/models/dashboard/dashboard-detail/dashboard-detail.model';

@Component({
  selector: 'tei-analytics-group-analytics-dashboard-summary',
  templateUrl: './dashboard-summary.component.html',
  styleUrls: ['./dashboard-summary.component.scss']
})
export class DashboardSummaryComponent {
  private _summary: SummarySection;
  @Input() isSmallScreen = false;
  emptyFilterMessageState = true;
  dataSource: SummaryMetricDisplay[] = [];
  metadata = new SummarySectionMetadata();
  SummaryMetricType = SummaryMetricType;

  @Input()
  set summary(value: SummarySection) {
    this._summary = value;
    if (value && value.Metrics) {
      this.dataSource = value.Metrics.map(metric => ({
        ...metric,
        varianceVsBudgetIconName: getTrendIconName(metric.VarianceVsBudgetIcon),
        varianceVsBudgetColor: getTrendIconColor(metric.VarianceVsBudgetIcon),
        varianceVsLastYearIconName: getTrendIconName(metric.VarianceVsLastYearIcon),
        varianceVsLastYearColor: getTrendIconColor(metric.VarianceVsLastYearIcon),
      }));
      this.emptyFilterMessageState = false;
    } else {
      this.dataSource = [];
      this.emptyFilterMessageState = true;
    }
  }
  get summary(): SummarySection {
    return this._summary;
  }

  constructor() {}
}
