import { Component, OnInit, Output, EventEmitter, Input, OnDestroy } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { debounceTime, distinctUntilChanged, takeUntil } from 'rxjs/operators';
import {
  DailySchedule,
  WeeklySchedule,
  MonthlySchedule,
  YearlySchedule,
  DayOfWeek,
  Month
} from '../../../@intermediate/models/reports/reports-generator.model';

enum GenerateOn {
  Now = 'now',
  Later = 'later',
  Recurring = 'recurring'
}

enum RecurrencePattern {
  Daily = 'daily',
  Weekly = 'weekly',
  Monthly = 'monthly',
  Yearly = 'yearly'
}

enum EndCondition {
  EndBy = 'endBy',
  EndAfter = 'endAfter',
  NoEndDate = 'noEndDate'
}

enum YearlyOption {
  OnDay = 'onDay',
  OnThe = 'onThe'
}

interface ScheduleFormModel {
  generateOn: GenerateOn;
  laterDateTime: Date | null;
  recurrencePattern: RecurrencePattern;
  // Daily pattern
  dailyOption: 'everyDay' | 'everyWeekday';
  dailyDays: number;
  // Weekly pattern
  weeklyRecurEvery: number;
  monday: boolean;
  tuesday: boolean;
  wednesday: boolean;
  thursday: boolean;
  friday: boolean;
  saturday: boolean;
  sunday: boolean;
  // Monthly pattern
  monthlyOption: 'Day' | 'weekly';
  dayValue: string;
  weekDay: string;
  numberOfDays: number;
  numberOfMonths: number;
  // Yearly pattern
  yearlyRecurEvery: number;
  yearlyOption: YearlyOption;
  yearlyMonth: string;
  yearlyDay: number;
  yearlyPosition: string;
  yearlyWeekday: string;
  yearlyMonthForWeekday: string;
  // Range
  startDate: Date;
  endCondition: EndCondition;
  endDateRange: Date;
  endAfterOccurrences: number;
}

interface ScheduleJobModel {
  StartDate: string;
  EndDate: string | null;
  EndAfterOccurrences: number;
  Daily: DailySchedule | null;
  Weekly: WeeklySchedule | null;
  Monthly: MonthlySchedule | null;
  Yearly: YearlySchedule | null;
}

interface FormattedScheduleValue {
  EffectiveTime: string | null;
  JobScheduleModel: ScheduleJobModel | null;
}

@Component({
  selector: 'tei-analytics-group-analytics-schedule',
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.scss']
})
export class ScheduleComponent implements OnInit, OnDestroy {
  @Input() parentForm!: FormGroup;
  @Output() scheduleFormReady = new EventEmitter<FormGroup>();
  @Output() scheduleFormValueChange = new EventEmitter<any>();

  private readonly destroy$ = new Subject<void>();
  private readonly DEBOUNCE_TIME = 300;

  readonly positionOptions: string[] = ['first', 'second', 'third', 'fourth', 'last'];
  readonly weekDays: string[] = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'];
  readonly months: string[] = Object.keys(Month).filter(key => isNaN(Number(key)));

  readonly GenerateOn = GenerateOn;
  readonly RecurrencePattern = RecurrencePattern;
  readonly EndCondition = EndCondition;
  readonly YearlyOption = YearlyOption;
  readonly DayOfWeek = DayOfWeek;

  scheduleForm: FormGroup = this.initializeForm();

  constructor(private readonly fb: FormBuilder) {}

  ngOnInit(): void {
    this.setupFormSubscriptions();
    this.scheduleFormReady.emit(this.scheduleForm);
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  getFormValue(): ScheduleFormModel {
    return this.scheduleForm.value;
  }

  isFormValid(): boolean {
    return this.scheduleForm.valid;
  }

  getErrorMessage(controlName: string): string {
    const control = this.scheduleForm.get(controlName);
    if (!control) return '';

    if (control.hasError('required')) return 'This field is required';
    if (control.hasError('min')) return `Value must be at least ${control.errors?.['min']?.min}`;
    if (control.hasError('max')) return `Value must not exceed ${control.errors?.['max']?.max}`;
    return '';
  }

  private setupFormSubscriptions(): void {
    this.setupValueChangeSubscription();
    this.setupConditionalValidation();
  }

  private setupValueChangeSubscription(): void {
    this.scheduleForm.valueChanges.pipe(
      takeUntil(this.destroy$),
      debounceTime(this.DEBOUNCE_TIME),
      distinctUntilChanged((prev, curr) => JSON.stringify(prev) === JSON.stringify(curr))
    ).subscribe((value: ScheduleFormModel) => {
      if (this.scheduleForm.valid) {
        this.scheduleFormValueChange.emit(this.getFormattedValue(value));
      }
    });
  }

  private setupConditionalValidation(): void {
    this.setupGenerateOnValidation();
    this.setupEndConditionValidation();
  }

  private setupGenerateOnValidation(): void {
    this.scheduleForm.get('generateOn')?.valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe(value => {
        this.updateLaterDateTimeValidation(value);
        this.updateRecurringValidation(value);
      });
  }

  private updateLaterDateTimeValidation(value: GenerateOn): void {
    const control = this.scheduleForm.get('laterDateTime');
    if (!control) return;

    if (value === GenerateOn.Later) {
      control.setValidators([Validators.required]);
    } else {
      control.clearValidators();
    }
    control.updateValueAndValidity();
  }

  private updateRecurringValidation(value: GenerateOn): void {
    const recurrenceControl = this.scheduleForm.get('recurrencePattern');
    const startDateControl = this.scheduleForm.get('startDate');

    if (!recurrenceControl || !startDateControl) return;

    if (value === GenerateOn.Recurring) {
      recurrenceControl.setValidators([Validators.required]);
      startDateControl.setValidators([Validators.required]);
    } else {
      recurrenceControl.clearValidators();
      startDateControl.clearValidators();
    }

    recurrenceControl.updateValueAndValidity();
    startDateControl.updateValueAndValidity();
  }

  private setupEndConditionValidation(): void {
    this.scheduleForm.get('endCondition')?.valueChanges
      .pipe(takeUntil(this.destroy$))
      .subscribe(value => {
        this.updateEndDateValidation(value);
      });
  }

  private updateEndDateValidation(value: EndCondition): void {
    const endDateControl = this.scheduleForm.get('endDateRange');
    const occurrencesControl = this.scheduleForm.get('endAfterOccurrences');

    if (!endDateControl || !occurrencesControl) return;

    if (value === EndCondition.EndBy) {
      endDateControl.setValidators([Validators.required]);
      occurrencesControl.clearValidators();
    } else if (value === EndCondition.EndAfter) {
      endDateControl.clearValidators();
      occurrencesControl.setValidators([Validators.required, Validators.min(1)]);
    } else {
      endDateControl.clearValidators();
      occurrencesControl.clearValidators();
    }

    endDateControl.updateValueAndValidity();
    occurrencesControl.updateValueAndValidity();
  }

  private initializeForm(): FormGroup {
    const tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);
    tomorrow.setHours(0, 0, 0, 0);

    return this.fb.group({
      generateOn: [GenerateOn.Now, [Validators.required]],
      laterDateTime: [tomorrow],
      recurrencePattern: [RecurrencePattern.Daily],
      // Daily pattern
      dailyOption: ['everyDay'],
      dailyDays: [1, [Validators.min(1), Validators.max(365)]],
      // Weekly pattern
      weeklyRecurEvery: [1, [Validators.min(1), Validators.max(52)]],
      monday: [false],
      tuesday: [false],
      wednesday: [false],
      thursday: [false],
      friday: [false],
      saturday: [false],
      sunday: [false],
      // Monthly pattern
      monthlyOption: ['Day'],
      dayValue: [this.positionOptions[0]],
      weekDay: [this.weekDays[0]],
      numberOfDays: [1, [Validators.min(1), Validators.max(31)]],
      numberOfMonths: [1, [Validators.min(1), Validators.max(12)]],
      // Yearly pattern
      yearlyRecurEvery: [1, [Validators.min(1), Validators.max(99)]],
      yearlyOption: [YearlyOption.OnDay],
      yearlyMonth: [this.months[0]],
      yearlyDay: [1, [Validators.min(1), Validators.max(31)]],
      yearlyPosition: [this.positionOptions[0]],
      yearlyWeekday: [this.weekDays[0]],
      yearlyMonthForWeekday: [this.months[0]],
      // Range
      startDate: [new Date()],
      endCondition: [EndCondition.NoEndDate],
      endDateRange: [null],
      endAfterOccurrences: [10]
    });
  }

  private getFormattedValue(value: ScheduleFormModel): FormattedScheduleValue {
    const result: FormattedScheduleValue = {
      EffectiveTime: null,
      JobScheduleModel: null
    };

    switch (value.generateOn) {
      case GenerateOn.Now:
        break;

      case GenerateOn.Later:
        if (value.laterDateTime) {
          result.EffectiveTime = new Date(value.laterDateTime).toISOString();
        }
        break;

      case GenerateOn.Recurring:
        result.JobScheduleModel = this.createJobScheduleModel(value);
        break;
    }

    return result;
  }

  private createJobScheduleModel(value: ScheduleFormModel): ScheduleJobModel {
    const model: ScheduleJobModel = {
      StartDate: new Date(value.startDate).toISOString(),
      EndDate: value.endCondition === EndCondition.EndBy ? new Date(value.endDateRange).toISOString() : null,
      EndAfterOccurrences: value.endCondition === EndCondition.EndAfter ? value.endAfterOccurrences : 0,
      Daily: null,
      Weekly: null,
      Monthly: null,
      Yearly: null
    };

    switch (value.recurrencePattern) {
      case RecurrencePattern.Daily:
        model.Daily = this.createDailySchedule(value);
        break;

      case RecurrencePattern.Weekly:
        model.Weekly = this.createWeeklySchedule(value);
        break;

      case RecurrencePattern.Monthly:
        model.Monthly = this.createMonthlySchedule(value);
        break;

      case RecurrencePattern.Yearly:
        model.Yearly = this.createYearlySchedule(value);
        break;
    }

    return model;
  }

  private createDailySchedule(value: ScheduleFormModel): DailySchedule {
    return {
      dailyIntervalRepeat: value.dailyOption === 'everyWeekday' ? 1 : value.dailyDays
    };
  }

  private createWeeklySchedule(value: ScheduleFormModel): WeeklySchedule {
    const selectedDays: DayOfWeek[] = [];
    if (value.monday) selectedDays.push(DayOfWeek.Monday);
    if (value.tuesday) selectedDays.push(DayOfWeek.Tuesday);
    if (value.wednesday) selectedDays.push(DayOfWeek.Wednesday);
    if (value.thursday) selectedDays.push(DayOfWeek.Thursday);
    if (value.friday) selectedDays.push(DayOfWeek.Friday);
    if (value.saturday) selectedDays.push(DayOfWeek.Saturday);
    if (value.sunday) selectedDays.push(DayOfWeek.Sunday);

    return {
      weeklyIntervalRepeat: value.weeklyRecurEvery,
      dayOfWeek: selectedDays
    };
  }

  private createMonthlySchedule(value: ScheduleFormModel): MonthlySchedule {
    return value.monthlyOption === 'Day'
      ? {
          dayOfMonth: value.numberOfDays,
          monthlyIntervalRepeat: value.numberOfMonths,
          weekNumber: null,
          dayOfWeek: []
        }
      : {
          dayOfMonth: null,
          monthlyIntervalRepeat: value.numberOfMonths,
          weekNumber: this.getWeekNumber(value.dayValue),
          dayOfWeek: [this.getDayOfWeek(value.weekDay)]
        };
  }

  private createYearlySchedule(value: ScheduleFormModel): YearlySchedule {
    return value.yearlyOption === YearlyOption.OnDay
      ? {
          yearlyIntervalRepeat: value.yearlyRecurEvery,
          monthOfYear: Month[value.yearlyMonth as keyof typeof Month],
          dayOfMonth: value.yearlyDay,
          weekNumber: null,
          dayOfWeek: []
        }
      : {
          yearlyIntervalRepeat: value.yearlyRecurEvery,
          monthOfYear: Month[value.yearlyMonthForWeekday as keyof typeof Month],
          weekNumber: this.getWeekNumber(value.yearlyPosition),
          dayOfWeek: [this.getDayOfWeek(value.yearlyWeekday)],
          dayOfMonth: null
        };
  }

  private getWeekNumber(position: string): number {
    const index = this.positionOptions.indexOf(position);
    return index + 1;
  }

  private getDayOfWeek(day: string): DayOfWeek {
    return DayOfWeek[day as keyof typeof DayOfWeek];
  }
}
