import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatCardModule } from '@angular/material/card';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { SecureNavigationModule } from '@tei/material-smart-components';
import { SettingsNavigationComponent } from './settings-navigation.component';

const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatCardModule];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule, SecureNavigationModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const COMPONENTS = [SettingsNavigationComponent];


@NgModule({
  declarations: [
    ...COMPONENTS,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
  ],
  exports: [
    ...COMPONENTS,
    ...TEI_MATERIAL_MODULES
  ]
})
export class SettingsNavigationModule { }
