import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyOptionModule } from '@angular/material/legacy-core';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import {
  AvatarModule,
  BreadCrumbModule,
  LoadingModule
} from '@tei/material';
import { DashboardWidgetModule } from '../../../components/dashboard-widget/dashboard-widget.module';
import { DashboardsRoutingModule } from './dashboards-routing.module';
import { DashboardsComponent } from './dashboards.component';
import { AnalyticsDashboardService } from '../../../@services/reports/dashboard/dashboard.service';
import { DashboardDetailService } from '../../../@services/reports/dashboard/dashboard-detail.service';
import { DashboardDetailsModule } from './dashboard-details/dashboard-details.module';

const COMPONENTS = [DashboardsComponent];
const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatCardModule, MatButtonModule, LoadingModule, MatInputModule, MatButtonToggleModule];
const TEI_MATERIAL_MODULES = [BreadCrumbModule, AvatarModule, DashboardsRoutingModule, MatSlideToggleModule, MatLegacyOptionModule,
  MatLegacySelectModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const ANALYTICS_MODULES = [DashboardWidgetModule, DashboardDetailsModule];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ANALYTICS_MODULES
  ],
  providers: [
    { provide: 'IAnalyticsDashboardService', useClass: AnalyticsDashboardService },
    { provide: 'IAnalyticsDashboardDetailService', useClass: DashboardDetailService }
  ]
})
export class DashboardsModule { }
