

export class AnalyticsReportAccessMetaData
{
  readonly Columns: string[] = ['Verified', 'AnalyticsObjectID', 'ReportName', 'Description', 'Type', 'Department', 'Actions'];
}

// Request Model
export class GetPaginatedFilteredItemsRequest
{
  PortalID: number;
  AnalyticsObjectID: number;
  Name: string;
  AnalyticsType: number | number[];
  SearchTerm: string;
  DepartmentIdentifier: string | string[];
  CategoryUID: string;
  Status: number;
  PageNumber: number;
  PageSize: number;
  SortColumn: number;
  SortDirection: number;
  Verified: boolean;
}

// Response Model
export class AnalyticsObject
{
  AnalyticsObjectID: number;
  AnalyticsObjectUID: string;
  Department: string;
  AnalyticsType: AnalyticsTypeEnum;
  Name: string;
  Description: string;
  Status: StatusEnum;
  TenantAnalyticsObjectID: number;
  Verified: boolean;
}

export class AnalyticsObjectCategory
{
  CategoryUID: string;
  AnalyticsObjectID: number;
}

export interface GetPaginatedFilteredItemsResponse
{
  ItemsCount: number;
  AnalyticsObjects: AnalyticsObject[];
  AnalyticsObjectCategories: AnalyticsObjectCategory[];
}

export enum AnalyticsTypeEnum
{
  Dashboard = 1,
  PDF = 2,
  DataView = 3
}

export const AnalyticsTypeDictionary: Record<AnalyticsTypeEnum, string> = {
  [AnalyticsTypeEnum.Dashboard]: 'Dashboard',
  [AnalyticsTypeEnum.PDF]: 'PDF',
  [AnalyticsTypeEnum.DataView]: 'Dataview',
};

export enum SortDirectionTypeEnum
{
  ASC,
  DESC
}

export enum SortColumnTypeEnum
{
  AnalyticsObjectID,
  Name,
  Description,
}

export enum StatusEnum
{
  Disabled = 0,
  Enabled = 1
}

export class Department
{
  DepartmentID: number;
  DepartmentIdentifier: string;
  Name: string;
  Description: string;
  SortOrder: number;
}

export class Type
{
  Value: number;
  Label: string;
}

export class DepartmentTypesResponse
{
  Departments: Department[];
  Types: Type[];
}

export class GetDepartmentTypesRequest
{
  PortalID: number;
}

export class PostVisibilityRequest
{
  PortalID: number;
  AnalyticsObjectUID: string;
  Status: number;
}
