import { Component, Inject, OnInit } from '@angular/core';
import { Sort } from '@angular/material/sort';
import { ActivatedRoute, Router } from '@angular/router';
import { ResponsiveService } from '@tei/core';
import { firstValueFrom } from 'rxjs';
import { AnalyticsTypeDictionary, AnalyticsTypeEnum } from '../../../@intermediate/models/analytics-report-access/analytics-report-access.model';
import { IAnalyticsReportsService } from '../../../@intermediate/models/reports/new-reports.interface';
import { AnalyticsObject, AnalyticsObjectCategory, AnalyticType, GetNewReportsRequest, GetNewReportsResponse, NewReportMetadata, SortColumnTypeEnum, SortDirectionTypeEnum } from '../../../@intermediate/models/reports/new-reports.model';

@Component({
  selector: 'tei-analytics-group-analytics-new-report',
  templateUrl: './new-report.component.html',
  styleUrls: ['./new-report.component.scss'],
})
export class NewReportComponent implements OnInit
{
  isSmallScreen = false;
  emptyFilterMessageState = true;
  dataSource = new Array<AnalyticsObject>();
  selectedRow: AnalyticsObject = null;
  metadata = new NewReportMetadata();
  totalCount = 0;
  pageIndex = 0;
  pageSize = 10;
  isLoading: boolean;
  AnalyticsTypeEnum = AnalyticsTypeEnum;
  selectedReportID: string = null;
  Categories: AnalyticsObjectCategory[] = [];
  analyticsTypeDictionary = AnalyticsTypeDictionary;
  request: GetNewReportsRequest = new GetNewReportsRequest();
  constructor(
    @Inject('IAnalyticsReportsService') private newReportService: IAnalyticsReportsService,
    private responsiveService: ResponsiveService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.detectScreenSize();
    await this.loadInitialData();

  }

  selectRow(row: AnalyticsObject): void
  {
    this.selectedRow = row;
    this.selectedReportID = row.AnalyticsObjectUID;
  };

  detectScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = ['xs', 'sm', 'md'].includes(screenSize);
  }

  async loadHandler(request: GetNewReportsRequest): Promise<void>
  {
    this.selectedRow = null;
    this.request = request;
    this.resetPaging();
    const response = await this.load();
    if (response && response.ItemsCount > 0)
    {
      this.dataSource = response.AnalyticsObjects;
      this.totalCount = response.ItemsCount;
      this.emptyFilterMessageState = false;
    }
    else
    {
      this.handleEmptyRequest();
    }
  }


  private resetPaging(): void
  {
    this.pageIndex = 0;
    this.request.PageSize = this.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
  };

  async loadInitialData(): Promise<void>
  {

    this.selectedRow = null;
    this.request.PageSize = this.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
    const response = await this.load();

    if (response && response.ItemsCount > 0)
    {
      this.dataSource = response.AnalyticsObjects;
      this.totalCount = response.ItemsCount;
      this.emptyFilterMessageState = false;
    } else
    {
      this.handleEmptyRequest();
    }
  }

  async load(): Promise<GetNewReportsResponse | void>
  {
    try
    {
      this.isLoading = true;
      const response = await firstValueFrom(
        this.newReportService.getNewReportListData(this.request)
      );
      return response;

    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.isLoading = false;
    }
  }

  private handleEmptyRequest(): void
  {
    this.dataSource = [];
    this.Categories = [];
    this.totalCount = 0;
    this.selectedRow = null;
    this.emptyFilterMessageState = true;
  }

  async handlePaginationEvent(event: { pageIndex: number; pageSize: number; }): Promise<void>
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
    this.request.PageSize = this.pageSize;
    const response = await this.load();
    if (response && response.ItemsCount > 0)
    {
      this.dataSource = response.AnalyticsObjects;
      this.totalCount = response.ItemsCount;
      this.emptyFilterMessageState = false;
    } else
    {
      this.handleEmptyRequest();
    }
  }

  Continue()
  {
    if (this.selectedRow.AnalyticsType === AnalyticType.Dashboard)
    {
      this.router.navigateByUrl('/office/analytics/dashboards');
      return;
    }

    this.router.navigate(['report-generator', this.selectedReportID], { relativeTo: this.activatedRoute.parent });
  }

  sortData(sort: Sort): void
  {
    if (!sort.active || sort.direction === '')
    {
      this.request.SortColumn = null;
      this.request.SortDirection = null;
    } else
    {
      let columnName = sort.active;
      if (columnName === 'ID')
      {
        columnName = 'ReportID';
      }

      this.request.SortColumn = SortColumnTypeEnum[columnName];
      this.request.SortDirection = sort.direction === 'asc' ? SortDirectionTypeEnum.ASC : SortDirectionTypeEnum.DESC;
    }
    this.loadHandler(this.request);
  }
}
