import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatRippleModule } from '@angular/material/core';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { MatSortModule } from '@angular/material/sort';
import { TranslateModule } from '@ngx-translate/core';
import { TeiEnumToLabelModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, FilterChipsModule, LoadingModule, QuickSearchFilterChipModule } from '@tei/material';
import { AnalyticsReportAccessService } from '../../../@services/analytics-report-access.service';
import { AnalyticsReportAccessSearchHeaderComponent } from './analytics-report-access-search-header/analytics-report-access-search-header.component';
import { AnalyticsReportAccessComponent } from './analytics-report-access.component';

const COMPONENTS = [AnalyticsReportAccessComponent, AnalyticsReportAccessSearchHeaderComponent];
const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [
  MatTableModule,
  MatIconModule,
  MatCardModule,
  MatDividerModule,
  MatTooltipModule,
  MatRippleModule,
  MatInputModule,
  MatFormFieldModule,
  MatPaginatorModule,
  MatSortModule
];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule, TeiEnumToLabelModule];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule, FilterChipsModule, QuickSearchFilterChipModule];


@NgModule({
  declarations: [
    ...COMPONENTS,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [
    { provide: 'IAnalyticsReportAccessService', useClass: AnalyticsReportAccessService }
  ]
})
export class AnalyticsReportAccessModule { }
