import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReportGeneratorComponent } from './report-generator.component';
import { ReportGeneratorService } from '../../../../@services/reports/report-generator.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { RouterModule } from '@angular/router';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, CardDialogModule, DynamicTableModule, LoadingModule, MultiChipModule, PhoneInputModule } from '@tei/material';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatNativeDateModule } from '@angular/material/core';
import { ParametersDynamicFormComponent } from '../../../../components/report-generator/parameters-dynamic-form/parameters-dynamic-form.component';
import { ScheduleComponent } from '../../../../components/report-generator/schedule/schedule.component';
import { NotficationComponent } from '../../../../components/report-generator/notfication/notfication.component';
import { DuplicateReportDialogComponent } from '../../../../components/report-generator/duplicate-report-dialog/duplicate-report-dialog.component';
import { MatLegacyProgressSpinnerModule as MatProgressSpinnerModule } from '@angular/material/legacy-progress-spinner';
import { MatLegacyAutocompleteModule as MatAutocompleteModule } from '@angular/material/legacy-autocomplete';
import { ContactGroupSearchModule, MultiContactAvatarModule } from '@tei/office-contacts';
import { MatLegacyDialogModule as MatDialogModule } from "@angular/material/legacy-dialog";
import { MatDividerModule } from "@angular/material/divider";
import { AdvancedLookupSearchModule } from '../../../../components/report-generator/advanced-lookup-search/advanced-lookup-search.module';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';

const COMPONENTS = [ReportGeneratorComponent,
                    ParametersDynamicFormComponent,
                    ScheduleComponent,
                    NotficationComponent,
                    DuplicateReportDialogComponent,
                    ];
const BASE_MODULES = [CommonModule, FormsModule, RouterModule, ReactiveFormsModule];
const MATERIAL_MODULES = [
  MatCardModule,
  MatButtonModule,
  MatFormFieldModule,
  MatInputModule,
  MatCheckboxModule,
  MatIconModule,
  MatDatepickerModule,
  MatChipsModule,
  MatSelectModule,
  MatRadioModule,
  MatSlideToggleModule,
  MatNativeDateModule,
  MatProgressSpinnerModule,
  MatAutocompleteModule,
  MultiChipModule,
  MatDialogModule,
  MatDividerModule,
  MatTooltipModule
];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule, PhoneInputModule, CardDialogModule, DynamicTableModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, SafeHtmlPipeModule];
const TEI_OFFICE_MODULES = [ContactGroupSearchModule, MultiContactAvatarModule ];
@NgModule({
  declarations: [
    ...COMPONENTS,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_OFFICE_MODULES,
    AdvancedLookupSearchModule
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [{
    provide: 'IReportGeneratorService', useClass: ReportGeneratorService
  }]
})
export class ReportGeneratorModule { }
