export class GetDashboardOperationDetailRequest {
  date?: string;
  formatType: OperationDetailFormatType;
}

export enum OperationDetailFormatType {
  Daily,
  Monthly,
  Yearly
}
export class GetDashboardOperationDetailResponse {
  GolfScorecardDashboardDetail: GolfScorecardDashboardModel;
}

export class GolfScorecardDashboardModel {
  Summary: SummarySection;
  Daily: DailySection;
}

export class SummarySection {
  Metrics: SummaryMetric[];
}

export enum SummaryMetricType {
  None,
  Currency
}
export class SummaryMetric {
  Type: SummaryMetricType;
  Detail: string;
  Value: number;
  Budget: number;
  VarianceVsBudgetPercent: number;
  VarianceVsBudgetIcon: TrendIconType;
  LastYear: number;
  VarianceVsLastYearPercent: number;
  VarianceVsLastYearIcon: TrendIconType;
}

export enum TrendIconType {
  TrendingDown,
  TrendingFlat,
  TrendingUp,
}
// Extend SummaryMetric for display
export interface SummaryMetricDisplay extends SummaryMetric {
  varianceVsBudgetIconName: string;
  varianceVsBudgetColor: string;
  varianceVsLastYearIconName: string;
  varianceVsLastYearColor: string;
}

// Helper to get icon name for Angular Material or similar icon set
export function getTrendIconName(type: TrendIconType): string {
  switch (type) {
    case TrendIconType.TrendingDown:
      return 'trending_down'; // Material icon name
    case TrendIconType.TrendingFlat:
      return 'trending_flat';
    case TrendIconType.TrendingUp:
      return 'trending_up';
    default:
      return '';
  }
}

// Helper to get color class or hex code
export function getTrendIconColor(type: TrendIconType): string {
  switch (type) {
    case TrendIconType.TrendingDown:
      return 'trend-down'; // CSS class for red
    case TrendIconType.TrendingFlat:
      return 'trend-flat'; // CSS class for gray
    case TrendIconType.TrendingUp:
      return 'trend-up'; // CSS class for green
    default:
      return '';
  }
}

export class DailySection {
  entries: DailyEntry[];
}

export class DailyEntry {
  date: string; // ISO date string (e.g., "2025-05-24T00:00:00Z")

  weather: WeatherType;
  weatherDescription: string;
  temperatureCelsius: number;

  rounds: number;
  greenFees: number;
  carts: number;
  cartFees: number;
  retailSales: number;

  detail?: DailyDetail; // optional, null unless expanded
}

export class DailyDetail {
  avgGreenFee: number;
  avgCartFee: number;
  avgRetail: number;

  priceLevels: PriceLevel[];
}

export class PriceLevel {
  description: string;
  totalRounds: number;
  totalSales: number;
}


export enum WeatherType {
  Sunny,
  Cloudy,
  Rainy,
  Stormy,
  Snowy,
  Windy,
  Foggy,
  Hazy
}

export class SummarySectionMetadata {
  readonly Columns: string[] = ['Detail', 'Value', 'Budget', 'Variance', 'Last_Year', 'Variance_Last_Year'];
}
