import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';
import { SecureNavigation } from '@tei/material-smart-components';

@Component({
  selector: 'tei-analytics-group-analytics-reports-navigation',
  templateUrl: './reports-navigation.component.html',
  styleUrls: ['./reports-navigation.component.scss']
})
export class ReportsNavigationComponent implements OnInit
{
  breadCrumbData: BreadCrumb[] = [];
  secureNavigation: SecureNavigation = {
    groupPermissionIdentifier: 'analytics-reports',
    sections: [
      {
        items: [
          {
            permissionIdentifier: "new-report",
            link: "new-reports",
            title: this.translate.instant("ANALYTICS.REPORTS.REPORTS_NAV.NEW_REPORT"),
            description: this.translate.instant("ANALYTICS.REPORTS.REPORTS_NAV.NEW_REPORTS_DESC")
          },
          {
            permissionIdentifier: "my-report",
            link: "my-reports",
            title: this.translate.instant("ANALYTICS.REPORTS.REPORTS_NAV.MY_REPORTS"),
            description: this.translate.instant("ANALYTICS.REPORTS.REPORTS_NAV.MY_REPORTS_DESC")
          },
          // {
          //   permissionIdentifier: "my-recurring-report",
          //   link: "recurring-reports",
          //   title: this.translate.instant("ANALYTICS.REPORTS.REPORTS_NAV.MY_RECURRING_REPORTS"),
          //   description: this.translate.instant("ANALYTICS.REPORTS.REPORTS_NAV.MY_RECURRING_REPORTS_DESC")
          // }
        ]
      }
    ]
  };

  constructor(private translate: TranslateService) { }

  ngOnInit(): void
  {
    this.initializeBreadcrumbs();
  }

  private initializeBreadcrumbs(): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
        relativeUrl: ''
      },
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.REPORTS'),
        relativeUrl: ''
      }
    ];
  }
}
