import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { CategoryRequest, CategoryResponse, GetAnalyticsSecurityRequest, GetAnalyticsSecurityResponse, GetConfigResponse } from "../@intermediate/models/security/setting-security.model";
import { Observable, of } from "rxjs";
import { IAnalyticsSecurityService } from "../@intermediate/models/security/analytics-security.interface";
import { HttpClient, HttpHeaders } from "@angular/common/http";

@Injectable()
export class AnalyticsSecurityService implements IAnalyticsSecurityService {
  private readonly serviceUrl: string;
  constructor( @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
  protected http: HttpClient) {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Security';
  }

  getSecurityListData(request: GetAnalyticsSecurityRequest): Observable<GetAnalyticsSecurityResponse> {
    const apiUrl = this.serviceUrl + '/GetPaginatedFilteredItems';
    return this.http.post<GetAnalyticsSecurityResponse>(apiUrl,request);
  }
  getAnalyticSecurityConfig(): Observable<GetConfigResponse> {
    const apiUrl = this.serviceUrl + '/config';
    return this.http.get<GetConfigResponse>(apiUrl);
  }
  addCategory(request: CategoryRequest): Observable<CategoryResponse> {
    const apiUrl = this.serviceUrl + '/AddCategory';
    return this.http.post<CategoryResponse>(apiUrl,request);
  }
  deleteCategory(request: CategoryRequest): Observable<CategoryResponse>
  {
    const options = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      body: request
    };
    const apiUrl = this.serviceUrl + '';
    return this.http.delete<CategoryResponse>(apiUrl,options);
  }
}
