import { Inject, Injectable } from "@angular/core";
import { IAnalyticCategoryService } from "../@intermediate/models/category/analytic-category.interface";
import { Observable } from "rxjs";
import { GetCategoriesResponse, DeleteCategoryRequest, DeleteCategoryResponse, PostDetailedCategoryRequest, PostDetailedCategoryResponse, FullCategoryWithRolesResponse, UpdateCategorySortOrderResponse, UpdateCategorySortOrderRequest } from "../@intermediate/models/category/analytic-category.model";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { IAPIBaseUrlProvider } from "@tei/core";

@Injectable()

export class ManageCategoryService implements IAnalyticCategoryService {
  private readonly serviceUrl: string;
    constructor( @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient) {
      this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Category';
    }

  getCategory(): Observable<GetCategoriesResponse> {
    const apiUrl = this.serviceUrl;
       return this.http.get<GetCategoriesResponse>(apiUrl);
  }
  deleteCategory(request: DeleteCategoryRequest): Observable<DeleteCategoryResponse> {
   const options = {
         headers: new HttpHeaders({
           'Content-Type': 'application/json'
         }),
         body: request
       };
       const apiUrl = this.serviceUrl + '';
       return this.http.delete<DeleteCategoryResponse>(apiUrl,options);
  }
  postCategory(request: PostDetailedCategoryRequest): Observable<PostDetailedCategoryResponse> {
    const apiUrl = this.serviceUrl;
    return this.http.post<PostDetailedCategoryResponse>(apiUrl,request);
  }
  getCategoryWithRoles(): Observable<FullCategoryWithRolesResponse> {
     const apiUrl = this.serviceUrl + '/WithRoles';
     return this.http.get<FullCategoryWithRolesResponse>(apiUrl);
  }
  postReOrder(request: UpdateCategorySortOrderRequest): Observable<UpdateCategorySortOrderResponse> {
    const apiUrl = this.serviceUrl + '/Reorder';
        return this.http.post<UpdateCategorySortOrderResponse>(apiUrl,request);
  }

}
