import { Component, Inject, OnInit } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { TodoItemFlatNode } from '../../../@intermediate/models/category/analytic-category.model';

@Component({
  selector: 'tei-analytics-group-analytics-delete-category-dialog',
  templateUrl: './delete-category-dialog.component.html',
  styleUrls: ['./delete-category-dialog.component.scss']
})
export class DeleteCategoryDialogComponent implements OnInit
{
  category = new TodoItemFlatNode();
  constructor(public dialogRef: MatDialogRef<DeleteCategoryDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any)
  { }

  ngOnInit(): void
  {
    if (this.data?.category)
    {
      this.category = this.data.category;
    }
  }
  closeDialog(): void
  {
    this.dialogRef.close(false);
  }
}
