import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardDetailsComponent } from './dashboard-details.component';
import { DashboardDetailService } from '../../../../@services/reports/dashboard/dashboard-detail.service';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatIconModule } from '@angular/material/icon';
import { MatExpansionModule } from '@angular/material/expansion';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, LoadingModule } from '@tei/material';
import { ReactiveFormsModule } from '@angular/forms';
import { DetailsFilterHeaderModule } from '../../../../components/dashboard-detail/details-filter-header/details-filter-header.module';
import { DashboardSummaryModule } from '../../../../components/dashboard-detail/dashboard-summary/dashboard-summary.module';
const COMPONENTS = [DashboardDetailsComponent];
const BASE_MODULES = [CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatIconModule, MatExpansionModule, LoadingModule, BreadCrumbModule];
const TEI_MATERIAL_MODULES = [MatTableModule, MatCardModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule];
const ANALYTICS_MODULES = [DetailsFilterHeaderModule, DashboardSummaryModule];

@NgModule({
  declarations: [
    ...COMPONENTS
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...ANALYTICS_MODULES
  ],
  providers: [
    { provide: 'IAnalyticsDashboardDetailService', useClass: DashboardDetailService }
  ]
})
export class DashboardDetailsModule { }
