import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { Subscription } from 'rxjs';
import
{
  ComponentTypeEnum,
  DateRange,
  DateRangeFormValue,
  FieldValue,
  ReportDynamicField,
  ReportDynamicFieldPost
} from '../../../@intermediate/models/reports/reports-generator.model';
import { AdvancedLookupSearchComponent } from '../advanced-lookup-search/advanced-lookup-search.component';
@Component({
  selector: 'tei-analytics-group-analytics-parameters-dynamic-form',
  templateUrl: './parameters-dynamic-form.component.html',
  styleUrls: ['./parameters-dynamic-form.component.scss']
})
export class ParametersDynamicFormComponent implements OnInit, OnDestroy
{
  @Input() reportForm!: FormGroup;
  @Input() set Fields(value: ReportDynamicField[])
  {
    this._fields = value?.filter(field => field !== null) || [];
  }
  get Fields(): ReportDynamicField[]
  {
    return this._fields;
  }
  private _fields: ReportDynamicField[] = [];
  subscription = new Subscription();

  @Output() parametersFormValueChange = new EventEmitter<ReportDynamicFieldPost[]>();

  ComponentTypeEnum = ComponentTypeEnum;

  constructor(private dialog: MatDialog,) { }

  private isDateRangeFormValue(value: any): value is DateRangeFormValue
  {
    return value && typeof value === 'object' && 'start' in value && 'end' in value;
  }

  ngOnDestroy(): void
  {
    this.subscription?.unsubscribe();
  }

  ngOnInit(): void
  {
    if (this.reportForm)
    {
      this.reportForm.valueChanges.subscribe(value =>
      {
        const formattedValue: ReportDynamicFieldPost[] = this._fields.map(field =>
        {
          const formValue = this.reportForm.get(field.FieldID)?.value;

          let selectedValue: FieldValue = null;
          let selectedRangeValue: DateRange | null = null;

          if (field.ComponentType === ComponentTypeEnum.RangeDatePicker && this.isDateRangeFormValue(formValue))
          {
            if (formValue.start && formValue.end)
            {
              selectedRangeValue = {
                start: formValue.start,
                end: formValue.end
              };
            }
          } else if (field.ComponentType === ComponentTypeEnum.DatePicker && formValue instanceof Date)
          {

            selectedValue = formValue;
          } else if (field.ComponentType === ComponentTypeEnum.NumberInput && typeof formValue === 'number')
          {
            selectedValue = formValue;
          } else if (field.ComponentType === ComponentTypeEnum.Lookup)
          {
            if (formValue)
            {
              selectedValue = formValue?.join(',')?.toString();
            }
          } else if (typeof formValue === 'string')
          {
            selectedValue = formValue;
          }

          return {
            FieldID: field.FieldID,
            SelectedValue: selectedValue,
            SelectedRangeValue: selectedRangeValue,
            ParamIndex: field.ParamIndex,
            ParamName: field.ParamName,
            StepNum: field.StepNum
          };
        });
        this.parametersFormValueChange.emit(formattedValue);
      });
    }
  }

  compareTwoDates(): boolean
  {
    const startDate = this.reportForm.get('StartDateTime')?.value;
    const endDate = this.reportForm.get('EndDateTime')?.value;
    if (startDate && endDate)
    {
      return new Date(endDate) < new Date(startDate);
    }
    return false;
  }

  isFieldRequired(field: ReportDynamicField): boolean
  {
    return field?.IsRequired || false;
  }

  getErrorMessage(field: ReportDynamicField): string
  {
    if (!field) return '';

    const control = this.reportForm.get(field.FieldID);
    if (control?.hasError('required'))
    {
      return `${ field.Label } is required`;
    }
    if (control?.hasError('email'))
    {
      return 'Please enter a valid email address';
    }
    if (control?.hasError('min'))
    {
      return `Value must be greater than ${ control.errors?.['min']?.min }`;
    }
    if (control?.hasError('max'))
    {
      return `Value must be less than ${ control.errors?.['max']?.max }`;
    }
    return '';
  }

  openLookupDialog(field: ReportDynamicField): void
  {
    const dialogRef = this.dialog.open(AdvancedLookupSearchComponent, {
      panelClass: ['o-advanced-lookup-search-dialog', 'o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      data: { field, selectedValues: this.reportForm.get(field.FieldID)?.value },
      width: '1000px',
    });

    this.subscription.add(dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.reportForm.get(field.FieldID)?.setValue(result);
        this.reportForm.get(field.FieldID + '_displayName')?.setValue(result?.join(', '));
      } else
      {
        this.reportForm.get(field.FieldID)?.setValue(null);
        this.reportForm.get(field.FieldID + '_displayName')?.setValue('');
      }
    }));
  }
}
