import { Observable } from "rxjs";
import { IAnalyticsReportsService } from "../../@intermediate/models/reports/new-reports.interface";
import { GetNewReportsRequest, GetNewReportsResponse, GetConfigResponse, GetParamsRequest, GetParamsResponse, AddReportRequestData, AddReportResponse } from "../../@intermediate/models/reports/new-reports.model";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";

@Injectable()
export class NewReportsService implements IAnalyticsReportsService {
   private readonly serviceUrl: string;
    constructor( @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    protected http: HttpClient) {
      this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Analytics/Report';
    }
  getNewReportListData(request: GetNewReportsRequest): Observable<GetNewReportsResponse> {
    const apiUrl = this.serviceUrl + '/GetPaginatedFilteredItems';
    return this.http.post<GetNewReportsResponse>(apiUrl,request);
  }
  getConfig(): Observable<GetConfigResponse> {
    const apiUrl = this.serviceUrl + '/config';
    return this.http.get<GetConfigResponse>(apiUrl);
  }
  getParams(request: GetParamsRequest): Observable<GetParamsResponse> {
   const apiUrl = this.serviceUrl + '/params';
       return this.http.get<GetParamsResponse>(apiUrl, {params: request as any});
  }
  addReport(request: AddReportRequestData): Observable<AddReportResponse> {
    const apiUrl = this.serviceUrl;
       return this.http.post<AddReportResponse>(apiUrl,request);
  }

}
