import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ResponsiveService } from '@tei/core';
import { TeiChipsModel } from '@tei/material';
import { firstValueFrom } from 'rxjs';
import { AnalyticsTypeDictionary, AnalyticsTypeEnum } from '../../../@intermediate/models/analytics-report-access/analytics-report-access.model';
import { IAnalyticsSecurityService } from '../../../@intermediate/models/security/analytics-security.interface';
import { AnalyticsObject, CategoryRequest, GetAnalyticsSecurityRequest, GetAnalyticsSecurityResponse, SecurityMetadata, SortColumnTypeEnum, SortDirectionTypeEnum } from '../../../@intermediate/models/security/setting-security.model';

import { Sort } from '@angular/material/sort';
import { GetConfigResponse } from '../../../@intermediate/models/reports/new-reports.model';

@Component({
  selector: 'tei-analytics-group-analytics-security-list',
  templateUrl: './security-list.component.html',
  styleUrls: ['./security-list.component.scss'],
})
export class SecurityListComponent implements OnInit
{
  isSmallScreen = false;
  emptyFilterMessageState = true;
  teiChipData: TeiChipsModel = {
    allChips: [],
  };
  dataSource = new Array<AnalyticsObject>();
  metadata = new SecurityMetadata();
  totalCount = 0;
  pageIndex = 0;
  pageSize = 10;
  isLoading: boolean;
  AnalyticsTypeEnum = AnalyticsTypeEnum;
  analyticsTypeDictionary = AnalyticsTypeDictionary;
  request: GetAnalyticsSecurityRequest = new GetAnalyticsSecurityRequest();
  configs: GetConfigResponse;

  constructor(
    @Inject('IAnalyticsSecurityService') private analyticsSecurityService: IAnalyticsSecurityService,
    private formBuilder: FormBuilder,
    private responsiveService: ResponsiveService,
  ) { }

  async ngOnInit(): Promise<void>
  {
    this.initializeDataSourceWithEmptyForms();
    this.detectScreenSize();
    await this.initializeData();
  }


  private initializeDataSourceWithEmptyForms(): void
  {
    this.dataSource = this.dataSource.map(item => ({
      ...item,
      form: item.form ?? this.formBuilder.group({
        Categories: [[]]
      })
    }));
  }

  private async initializeData(): Promise<void>
  {
    await this.loadConfig();
    await this.loadInitialData();
  }

  detectScreenSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    this.isSmallScreen = ['xs', 'sm', 'md'].includes(screenSize);
  }

  async loadHandler(request: GetAnalyticsSecurityRequest): Promise<void>
  {
    this.request = request;
    this.resetPaging();
    await this.load();
  }

  private resetChipStates(): void
  {
    this.teiChipData.allChips.forEach(chip => chip.active = false);
  }

  private handleEmptyRequest(): void
  {
    this.dataSource = [];
    this.resetChipStates();
    this.totalCount = 0;
    this.emptyFilterMessageState = true;
  }

  displayCategoryLabel(selectedCategories: any[] = []): string
  {
    if (!selectedCategories?.length)
    {
      return 'Select Categories';
    }
    if (selectedCategories.length === 1)
    {
      return selectedCategories[0]?.name || 'Unknown';
    }
    const othersCount = selectedCategories.length - 1;
    const othersLabel = othersCount === 1 ? 'other' : 'others';
    return `${ selectedCategories[0]?.name || 'Unknown' } (+${ othersCount } ${ othersLabel })`;
  }

  async saveCategory(category: any, item: AnalyticsObject): Promise<void>
  {
    try
    {
      this.isLoading = true;
      await this.processCategoryUpdate(category, item);
    } catch (error)
    {
      console.error('Error updating category:', error);
    } finally
    {
      this.isLoading = false;
    }
  }

  private createCategoryRequest(item: AnalyticsObject, category: any): CategoryRequest
  {
    return {
      AnalyticsObjectUID: item.AnalyticsObjectUID,
      CategoryUID: category.categoryId
    };
  }

  private async processCategoryUpdate(category: any, item: AnalyticsObject): Promise<void>
  {
    const categories = item.form.controls['Categories'].value || [];

    const isSelected = categories.some(c => c.id === category.id);

    if (!isSelected)
    {
      await this.deleteCategory(category, item, categories);
    } else
    {
      await this.addCategory(category, item, categories);
    }
  }

  private async deleteCategory(category: any, item: AnalyticsObject, categories: any[]): Promise<void>
  {
    await firstValueFrom(this.analyticsSecurityService.deleteCategory(this.createCategoryRequest(item, category)));
    await this.load();
  }

  private async addCategory(category: any, item: AnalyticsObject, categories: any[]): Promise<void>
  {
    await firstValueFrom(this.analyticsSecurityService.addCategory(this.createCategoryRequest(item, category)));
    await this.load();
  }

  async removeCategory(chip: any, item: any): Promise<void>
  {
    try
    {
      this.isLoading = true;
      await firstValueFrom(
        this.analyticsSecurityService.deleteCategory(this.createCategoryRequest(item, chip))
      );

      const categories = item.form.controls['Categories'].value || [];
      item.form.controls['Categories'].setValue(
        categories.filter((c: any) => c.id !== chip.id)
      );
    } catch (error)
    {
    } finally
    {
      this.isLoading = false;
    }
  }

  private resetPaging(): void
  {
    this.pageIndex = 0;
    this.request.PageSize = this.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
  };

  private async loadInitialData(): Promise<void>
  {
    this.resetPaging();
    await this.load();
  }

  private async loadConfig()
  {
    try
    {
      this.isLoading = true;
      this.configs = await firstValueFrom(
        this.analyticsSecurityService.getAnalyticSecurityConfig()
      );

      this.teiChipData.allChips = this.configs?.Categories?.map((m) => ({
        id: m.CategoryID,
        name: m.Name,
        active: false,
        categoryId: m.CategoryUID,
        parentId: m.ParentID,
        sortOrder: m.SortOrder,
      })).sort((a, b) => a.name.localeCompare(b.name)) || [];


      return this.configs;
    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.isLoading = false;
    }
  }

  private async preselectCategories(response: GetAnalyticsSecurityResponse): Promise<void>
  {
    if (!response || !response.AnalyticsObjects)
    {
      console.warn('getSecurityListData() returned undefined or missing AnalyticsObjects.');
      return;
    }

    this.dataSource = response.AnalyticsObjects.map(obj =>
    {
      const selectedCategories = obj.AnalyticsObjectCategories
        .map(category => this.teiChipData.allChips.find(chip => chip.id === category.CategoryID))
        .filter(chip => chip !== undefined);

      return {
        ...obj,
        form: this.formBuilder.group({
          Categories: [selectedCategories]
        })
      };
    });
  }

  async handlePaginationEvent(event: { pageIndex: number; pageSize: number; }): Promise<void>
  {
    this.pageIndex = event.pageIndex;
    this.pageSize = event.pageSize;
    this.request.PageNumber = this.pageIndex + 1;
    this.request.PageSize = this.pageSize;
    const response = await this.load();
  }

  private async load(): Promise<GetAnalyticsSecurityResponse>
  {
    try
    {
      this.isLoading = true;

      if (!this.request.SortColumn) 
      {
        this.request.SortColumn = SortColumnTypeEnum['ReportID'];
        this.request.SortDirection = SortDirectionTypeEnum.ASC;
      }

      const response = await firstValueFrom(
        this.analyticsSecurityService.getSecurityListData(this.request)
      );

      if (response && response.ItemsCount > 0)
      {
        this.dataSource = response.AnalyticsObjects;
        this.totalCount = response.ItemsCount;
        this.emptyFilterMessageState = false;
        this.initializeDataSourceWithEmptyForms();
        await this.preselectCategories(response);
      } else
      {
        this.handleEmptyRequest();
      }

      return response;

    } catch (error)
    {
      console.error(error);
    } finally
    {
      this.isLoading = false;
    }
  }

  sortData(sort: Sort): void
  {
    if (!sort.active || sort.direction === '')
    {
      this.request.SortColumn = null;
      this.request.SortDirection = null;
    } else
    {
      let columnName = sort.active;
      if (columnName === 'ID')
      {
        columnName = 'ReportID';
      }

      this.request.SortColumn = SortColumnTypeEnum[columnName];
      this.request.SortDirection = sort.direction === 'asc' ? SortDirectionTypeEnum.ASC : SortDirectionTypeEnum.DESC;
    }
    this.loadHandler(this.request);
  }
}
