export interface DateRange {
  start: string | Date;
  end: string | Date;
}

// Form-specific types
export interface DateRangeFormValue {
  start: Date | null;
  end: Date | null;
}

export type FormValue = string | number | Date | DateRangeFormValue | null;
export type FieldValue = string | number | Date | null;

export interface ReportDynamicField {
  FieldID: string;
  Label: string;
  LookupSource: string;
  ComponentType: ComponentTypeEnum;
  SelectedValue: any | any[];  // Can be either a simple value or a DateRange
  SelectedRangeValue: DateRange | null;
  Placeholder: string | null;
  IsRequired: boolean;
  Validation: any;
  Options: any[];
  SubFields: any[];
  ParamIndex: number;
  ParamName: string;
  StepNum: number;
}

export interface ReportDynamicForm {
  FormName: string;
  FormID: string;
  Fields: ReportDynamicField[];
}

export class ReportValidationRules {
  MinLength?: number;
  MaxLength?: number;
  MinValue?: number;
  MaxValue?: number;
  RegexPattern?: string;
}

export class ReportOptionModel {
  Key: string;
  Value: any;
}

export enum ComponentTypeEnum {
  TextBox,
  TextArea,
  NumberInput,
  RangeNumberInput,
  DatePicker,
  RangeDatePicker,
  TimePicker,
  RangeTimePicker,
  Dropdown,
  MultiSelect,
  TreeSelect,
  Checkbox,
  RadioButton,
  FileUpload,
  Lookup,
  Group
}

