import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from "@angular/material/divider";
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyDialogModule as MatDialogModule } from "@angular/material/legacy-dialog";
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { RouterModule } from '@angular/router';
import { SafeHtmlPipeModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, CardDialogModule, DynamicTableModule, LoadingModule } from '@tei/material';
import { AnalyticsLookupService } from '../../../@services/reports/lookup/lookup.service';
import { AdvancedLookupSearchComponent } from './advanced-lookup-search.component';
import { ResponsiveTableHeightModule } from '@tei/core';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';

const COMPONENTS = [AdvancedLookupSearchComponent];
const BASE_MODULES = [CommonModule, FormsModule, RouterModule, ReactiveFormsModule, ResponsiveTableHeightModule];
const MATERIAL_MODULES = [
  MatCardModule,
  MatButtonModule,
  MatFormFieldModule,
  MatIconModule,
  MatDialogModule,
  MatDividerModule,
  MatCheckboxModule
];
const TEI_MATERIAL_MODULES = [LoadingModule, BreadCrumbModule, CardDialogModule, DynamicTableModule];
const TEI_CORE_MODULES = [TeiTextTranslatePipeModule, SafeHtmlPipeModule];

@NgModule({
  declarations: [
    ...COMPONENTS,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,

  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [{
    provide: 'IAnalyticsLookupService', useClass: AnalyticsLookupService
  }]
})
export class AdvancedLookupSearchModule { }
