import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { MatSortModule } from '@angular/material/sort';
import { ResponsiveTableHeightModule } from '@tei/core';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiEnumToLabelModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { FilterChipsModule, LoadingModule, MultipleSelectChipsModule, QuickSearchFilterChipModule } from '@tei/material';
import { NewReportsService } from '../../@services/reports/new-reports.service';
import { NewReportHeaderComponent } from './new-report-header/new-report-header.component';
import { NewReportComponent } from './new-report/new-report.component';
import { ReportsDetailsComponent } from './reports-details.component';

const COMPONENTS = [ReportsDetailsComponent, NewReportComponent, NewReportHeaderComponent];
const BASE_MODULES = [CommonModule];
const MATERIAL_MODULES = [MatTableModule, MatInputModule, MatIconModule, MatDividerModule, MatCardModule, MatRadioModule, MatCheckboxModule, MatPaginatorModule, MatTooltipModule, MatSortModule];
const TEI_CORE_MODULES = [TeiDateTranslateModule, ResponsiveTableHeightModule, TeiCurrencyTranslateModule, TeiTextTranslatePipeModule, MultipleSelectChipsModule, TeiEnumToLabelModule];
const TEI_MATERIAL_MODULES = [FilterChipsModule, QuickSearchFilterChipModule, LoadingModule];
@NgModule({
  declarations: [
    ...COMPONENTS,
  ],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_CORE_MODULES,
    ...TEI_MATERIAL_MODULES,
  ],
  exports: [
    ...COMPONENTS,
  ],
  providers: [{
    provide: 'IAnalyticsReportsService', useClass: NewReportsService
  }]
})
export class ReportsDetailsModule { }
