import { FormGroup } from "@angular/forms";


export class SecurityMetadata
{
  readonly Columns: string[] = ['ID', 'Name', 'Description', 'Type', 'Department', 'Categories'];
}

export class GetAnalyticsSecurityRequest
{
  PortalID?: number;
  ReportID?:string;
  AnalyticsObjectID?: number;
  Name?: string;
  AnalyticsType?: number;
  DepartmentIdentifier?: string | string[];
  CategoryUID?: string | string[];
  PageNumber: number;
  PageSize: number;
  SortColumn?: number;
  SortDirection?: number;
  SearchTerm?: string;
}

export class GetAnalyticsSecurityResponse
{
  ItemsCount: number;
  AnalyticsObjects: AnalyticsObject[];

}

export interface AnalyticsObject
{
  AnalyticsObjectID: number;
  AnalyticsObjectUID: string;
  Department: string;
  AnalyticsType: number;
  Name: string;
  Description: string;
  Status: number;
  TenantAnalyticsObjectID: number;
  Verified: boolean;
  AnalyticsObjectCategories: AnalyticsObjectCategory[];
  form?: FormGroup; // use in fe
  ReportID?: string;
}

export interface AnalyticsObjectCategory
{
  CategoryUID: string;
  AnalyticsObjectID: number;
  AnalyticsObjectUID: string;
  CategoryID: number;
}

export interface Department
{
  DepartmentID: number;
  DepartmentIdentifier: string;
  Name: string;
  Description: string;
  SortOrder: number;
}

export interface Type
{
  Value: string;
  Label: string;
}

export interface Category
{
  CategoryUID: string;
  CategoryID: number;
  ParentID: number;
  Name: string;
  SortOrder: number;
}

export class GetConfigResponse
{
  Departments: Department[];
  Types: Type[];
  Categories: Category[];
}

export interface CategoryRequest
{
  AnalyticsObjectUID: string;
  CategoryUID: string[];
}

export interface CategoryResponse
{

}

export enum SortColumnTypeEnum
{
  AnalyticsObjectID = 0,
  Name = 1,
  Description = 2,
  Department = 3,
  ReportID = 4
}

export enum SortDirectionTypeEnum
{
  ASC = 0,
  DESC = 1
}
