import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import moment from 'moment';
import { TranslateService } from '@ngx-translate/core';
import { GetDashboardOperationDetailRequest, OperationDetailFormatType } from '../../../@intermediate/models/dashboard/dashboard-detail/dashboard-detail.model';

@Component({
  selector: 'tei-analytics-group-analytics-details-filter-header',
  templateUrl: './details-filter-header.component.html',
  styleUrls: ['./details-filter-header.component.scss']
})
export class DetailsFilterHeaderComponent implements OnInit
{
  @Output() filterChange = new EventEmitter<GetDashboardOperationDetailRequest>();
  form: FormGroup;
  OperationDetailFormatType = OperationDetailFormatType;
  formatOptions = [
    { value: OperationDetailFormatType.Daily, label: this.translate.instant('ANALYTICS.DASHBOARDS.DASHBOARD_DETAIL.DAILY') },
    { value: OperationDetailFormatType.Monthly, label: this.translate.instant('ANALYTICS.DASHBOARDS.DASHBOARD_DETAIL.MONTH_TO_DATE') },
    { value: OperationDetailFormatType.Yearly, label: this.translate.instant('ANALYTICS.DASHBOARDS.DASHBOARD_DETAIL.YEAR_TO_DATE') }
  ];

  constructor(
    private fb: FormBuilder,
    private translate: TranslateService
  ) { }

  ngOnInit(): void
  {
    this.form = this.fb.group({
      formatType: [0],
      date: [moment().toDate()],
    });
  }

  onFormatChange() {
    const now = moment().toDate();
    this.form.patchValue({
      date: now,
    });
  }

  applyFilter()
  {
    if (this.form.valid)
    {
      const formatType = this.form.get('formatType')?.value;
      this.filterChange.emit({
        formatType,
        date: this.form.get('date')?.value.format('YYYY-MM-DD'), //  send as "2025-08-05"
      } as GetDashboardOperationDetailRequest);
    }
  }



  dailyFilter = (date: Date | null): boolean =>
  {
    if (!date)
    {
      return false;
    }
    return true; // Allow all dates for daily view
  }
}
