import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';

@Component({
  selector: 'tei-analytics-group-analytics-generation-log',
  templateUrl: './generation-log.component.html',
  styleUrls: ['./generation-log.component.scss']
})
export class GenerationLogComponent implements OnInit {
  breadCrumbData: BreadCrumb[] = [];
  isGeneratedLog: boolean = true;

  constructor(private translate: TranslateService) { }

  ngOnInit(): void {
    this.initializeBreadcrumbs();
    this.isGeneratedLog = true;
  }

  private initializeBreadcrumbs(): void {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.ANALYTICS'),
        relativeUrl: ''
      },
      {
        title: this.translate.instant('ANALYTICS.ANALYTICS_NAVIGATION.SETTINGS'),
        relativeUrl: '../'
      },
      {
        title: this.translate.instant('ANALYTICS.SETTINGS.GENERATION_LOG'),
        relativeUrl: ''
      }
    ];
  }
}
