import { Component, OnInit, Input, Output, EventEmitter, OnDestroy, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { NotificationType, NotificationValue } from '../../../@intermediate/models/reports/reports-generator.model';
import { Subscription } from 'rxjs';
import { IReportGeneratorService } from '../../../@intermediate/models/reports/reports-generator.interface';
import { Users } from '../../../@intermediate/models/reports/reports-generator.model';
import { ReportGeneratorService } from '../../../@services/reports/report-generator.service';
import { Contact, IContactProviderServiceConst } from '@tei/office-contacts';

@Component({
  selector: 'tei-analytics-group-analytics-notfication',
  templateUrl: './notfication.component.html',
  styleUrls: ['./notfication.component.scss'],
  providers: [
    {
      provide: IContactProviderServiceConst,
      useClass: ReportGeneratorService
    }
  ]
})
export class NotficationComponent implements OnInit, OnDestroy {
  @Input() parentForm!: FormGroup;
  @Output() notificationFormReady = new EventEmitter<FormGroup>();
  @Output() notificationFormValueChange = new EventEmitter<NotificationValue>();

  readonly NotificationType = NotificationType;

  notificationForm: FormGroup = this.initializeForm();
  staffUsers: Users[] = [];
  filteredStaffUsers: Users[] = [];
  staffUserSearchControl = new FormControl('');
  isLoading = false;
  selectedStaffUsers: Users[] = [];

  subscription = new Subscription();
  constructor(
    private fb: FormBuilder,
    @Inject('IReportGeneratorService') private reportGeneratorService: IReportGeneratorService,
  ) {}

  ngOnInit(): void {

    this.notificationFormReady.emit(this.notificationForm);
  }

  ngOnDestroy(): void {
    this.subscription?.unsubscribe();

  }

  addEmailChip(emails: Array<string>): void {
    this.notificationForm.patchValue({ emailList: emails }, { emitEvent: false });
    this.notificationFormValueChange.emit();
  }

  isFormValid(): boolean {
    const NotificationType = this.notificationForm.get('NotificationType')?.value;
    const emailList = this.notificationForm.get('emailList')?.value || [];
    return this.notificationForm.valid &&
           (NotificationType !== NotificationType.Email || emailList.length > 0) &&
           (NotificationType !== NotificationType.InApp || this.notificationForm.get('selectedStaffUsers')?.value?.length > 0);
  }


  private updateEmailListValidation(Notification: NotificationType): void {
    const emailListControl = this.notificationForm.get('emailList');
    if (!emailListControl) return;

    if (Notification === NotificationType.Email) {
      emailListControl.setValidators([Validators.required, Validators.minLength(1)]);
    } else {
      emailListControl.clearValidators();
    }
    emailListControl.updateValueAndValidity();
  }

  private initializeForm(): FormGroup {
    return this.fb.group({
      NotificationType: [NotificationType.None, [Validators.required]],
      emailList: [],
      selectedStaffUsers: []
    });
  }

  updateStaffUsers(users: Contact[]): void {
    const contactIds = users.map(user => user.ContactID);
    this.notificationForm.patchValue({ selectedStaffUsers: contactIds });
    this.notificationFormValueChange.emit();
  }

}
