import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { ChipItemModel, ChipOptionModel, ChipType } from '@tei/material';
import { catchError, concat, debounceTime, firstValueFrom, Observable, of, Subject, switchMap } from 'rxjs';
import { IAnalyticsReportAccessService } from '../../../../@intermediate/models/analytics-report-access/analytics-report-access.interface';
import { Department, GetDepartmentTypesRequest, GetPaginatedFilteredItemsRequest, Type } from '../../../../@intermediate/models/analytics-report-access/analytics-report-access.model';

@Component({
  selector: 'tei-analytics-group-analytics-analytics-report-access-search-header',
  templateUrl: './analytics-report-access-search-header.component.html',
  styleUrls: ['./analytics-report-access-search-header.component.scss']
})
export class AnalyticsReportAccessSearchHeaderComponent implements OnInit
{
  form: FormGroup;
  ChipType = ChipType;
  request: GetPaginatedFilteredItemsRequest = new GetPaginatedFilteredItemsRequest();
  overflowHidden = false;
  minTermLength = 0;
  statusItemsWithDisplayName = [
    {
      chipName: 'Status',
      displayName: this.translate.instant(
        'ANALYTICS.REPORTS.ANALYTICS_REPORT_ACCESS.STATUS'
      ),
    }
  ];
  statusList = [
    { Status: 'Enabled', Value: 1 },
    { Status: 'Disabled', Value: 0 }
  ];

  verifyItemsWithDisplayName = [
    {
      chipName: 'Verified',
      displayName: this.translate.instant(
        'ANALYTICS.REPORTS.ANALYTICS_REPORT_ACCESS.VERIFIED'
      ),
      bindValue: 'Value'
    }
  ];

  verifyList = [
    { Verified: 'Verified Only', Value: true },
    { Verified: 'All', Value: false }
  ];

  typeItemsWithDisplayName: ChipItemModel[] = [
    {
      chipName: 'Label',
      displayName: this.translate.instant(
        'ANALYTICS.REPORTS.ANALYTICS_REPORT_ACCESS.TYPE'
      ),
      bindValue: 'Value'
    } as ChipItemModel
  ];
  typeList: Type[] = [];
  typeListSubjectArray: Observable<Type[]> = of([]);
  typeListTypeAheadInput$ = [];

  @Input() portalId = 0;
  departmentList: Department[] = [];
  departmentListSubjectArray: Observable<Department[]> = of([]);
  departmentListTypeAheadInput$ = [];
  departmentWithDisplayName: ChipItemModel[] = [
    { chipName: 'Name', displayName: this.translate.instant('ANALYTICS.REPORTS.ANALYTICS_REPORT_ACCESS.DEPARTMENT'), bindValue: 'DepartmentIdentifier' } as ChipItemModel,
  ];

  @Output() enterFilterValueEvent = new EventEmitter<GetPaginatedFilteredItemsRequest>();
  constructor(
    public manageLoadingService: ManageLoadingService,
    private formBuilder: FormBuilder,
    private translate: TranslateService,
    @Inject('IAnalyticsReportAccessService')
    private reportAccessService: IAnalyticsReportAccessService,)
  { }


  async ngOnInit(): Promise<void>
  {
    this.form = this.formBuilder.group({
      searchedTerm: ['']
    });
    await this.getConfigDepartmentTypes();
    this.filterDepartmentByChipItem();
    this.filterTypeListByChipItem();
  }

  async getConfigDepartmentTypes(): Promise<void>
  {
    const request: GetDepartmentTypesRequest = {
      PortalID: this.portalId
    };
    const response = await firstValueFrom(this.reportAccessService.getConfigDepartmentTypes(request));
    this.departmentList = response.Departments;
    this.typeList = response.Types;
  }

  filterDepartmentByChipItem(): void
  {
    this.departmentListTypeAheadInput$.push(new Subject<string>());
    this.departmentListSubjectArray[0] = concat(
      of(this.departmentList),
      this.departmentListTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterDepartmentList(searchTerm as string).pipe(
            catchError(() => of(this.departmentList)),
          ))
      )
    );
  }

  filterDepartmentList(searchTerm: string): Observable<Array<Department>>
  {
    return of(this.departmentList.filter(item => item.Name.toLowerCase().includes(searchTerm)));
  }

  filterTypeListByChipItem(): void
  {
    this.typeListTypeAheadInput$.push(new Subject<string>());
    this.typeListSubjectArray[0] = concat(
      of(this.typeList),
      this.typeListTypeAheadInput$[0].pipe(
        debounceTime(300),
        switchMap((searchTerm = '') =>
          this.filterTypeList(searchTerm as string).pipe(
            catchError(() => of(this.typeList)),
          ))
      )
    );
  }

  filterTypeList(searchTerm: string): Observable<Array<Type>>
  {
    return of(this.typeList.filter(item => item.Label.toLowerCase().includes(searchTerm)));
  }


  onSearchChange(event: Event): void
  {
    this.request.SearchTerm = this.form.get('searchedTerm').value;
    event?.stopPropagation();
    event?.preventDefault();
    setTimeout(() => { this.load(); }, 300);
  }

  onSearchStatusChange(event: ChipItemModel): void
  {
    const selected = this.statusList.find(item => item.Status === event[0]?.selectedItem);
    this.request.Status = selected ? selected.Value : null;
    this.load();
  }

  onSearchVerifiedChange(event: ChipItemModel): void
  {

    if (event[0].value === true)
    {
      this.request.Verified = event[0].value;
    }
    else
    {
      this.request.Verified = null;
    }

    this.load();
  }

  onSearchNameChange(event: ChipOptionModel): void
  {
    this.request.Name = event?.firstValue;
    this.load();
  }

  onSearchIDChange(event: ChipOptionModel): void
  {
    this.request.AnalyticsObjectID = event?.firstValue;
    this.load();
  }

  onSearchTypeChange(event: ChipOptionModel): void
  {
    const selected = this.typeList.find(item => item.Label === event[0]?.selectedItem);
    this.request.AnalyticsType = selected ? selected.Value : null;
    this.load();
  }

  onFilterChange(filterObjects: Array<ChipItemModel>): void
  {

    filterObjects?.forEach((chipItem: ChipItemModel) =>
    {
      if (chipItem.chipName === 'Label')
      {
        this.request.AnalyticsType = chipItem.selectedItem.length > 0 ? Number(chipItem.value) : null;
      }
      if (chipItem.chipName === 'Name')
      {
        this.request.DepartmentIdentifier = chipItem.selectedItem.length > 0 ? chipItem.value : null;
      }
      this.load();
    });
  }

  load(): void
  {
    this.enterFilterValueEvent.emit(this.request);
  }

}
